import { Component, OnInit } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import {Observable} from 'rxjs/Observable';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';
import { environment } from '../../environments/environment.dev';

@Component({
  selector: 'app-addpolicies',
  templateUrl: './addpolicies.component.html',
  styleUrls: ['./addpolicies.component.css']
})
export class AddpoliciesComponent implements OnInit {
  public tpaID: TPAId;
  public org: Org[] = [];
  public ins: Ins[] = [];

  public p_name= '';
  public p_number= '';
  public p_s_assured= '';
  public p_org_id: number;
  public p_ins_id: number;
  public p_tpa_id: number;
  
  public p_image= '';
  public e_start_date= '';
  public e_end_date= '';
  public p_start_date= '';
  public p_end_date= '';
  public p_cd_balance = '';
  public p_premium_utilization = '';
  public p_coverage_type = '';
  public p_period = '';
  public p_benefits = '';
  public p_eligibility = '';
  public p_faqs = '';
  public p_pol_type = '';

  public errorflag = false;
  public errormsg = '';
  public buttonflag = false;

  public uploadlink: string[] = [];

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
    private http: Http
  ) { }

  ngOnInit() {
    // TPA init starts
    if (this.roleInfo.isSA === true || this.roleInfo.isBU === true || this.roleInfo.isCSM === true) {
      this.sender.getTPAId()
      .subscribe(
          data => {
            if (data.code === 200) {
              this.tpaID = data.data;
            }
          },
          error => {
  
          });
  
      this.sender.getOrgId()
      .subscribe(
          data => {
            if (data.code === 200) {
              this.org = data.data;
            }
          },
          error => {
  
          });
  
      this.sender.getInsId()
      .subscribe(
          data => {
            if (data.code === 200) {
              this.ins = data.data;
            }
          },
          error => {
  
          });
    }
    // TPA init ends
  }

// TPA utility starts
  addPolicyTPA () {
    console.log (this.tpaID.id + this.p_org_id + this.p_ins_id);
    this.sender.createPolicy (this.p_name, this.p_number, this.p_s_assured, this.tpaID.id, this.p_ins_id, this.p_org_id, this.p_coverage_type, this.p_premium_utilization, this.p_cd_balance, this.p_start_date, this.p_end_date, this.e_start_date, this.e_end_date, this.p_image, this.p_benefits, this.p_eligibility, this.p_faqs, this.p_period, this.p_pol_type)
    .subscribe(
      data => {
        if (data.code === 200) {
          this.errorflag = true;
          this.errormsg = data.message;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
          console.log (data.message);
        } else {
        this.errorflag = true;
        this.errormsg = data.message.errorInfo[2];
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
        console.log (data.message);
        }
      },
      error => {
        this.errorflag = true;
        this.errormsg = error;
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
      }
    );
  }


  fileUpload(event) {
    let fileList: FileList = event.target.files;
    let url = environment.API_URL + 'file/upload';
    let token = sessionStorage.getItem('Token');
    let n_token = token.replace(/['"]+/g, '');

    if(fileList.length > 0) {
        this.errormsg = 'Uploading file... Please Wait';
        this.buttonflag = true;
        let file: File = fileList[0];
        let formData:FormData = new FormData();
        
        formData.append('file', file, file.name);
        
        let headers = new Headers();
        /** No need to include Content-Type in Angular 4 */
        //headers.append('Content-Type', 'multipart/form-data');
        headers.append('Authorization', `Bearer ${n_token}`);
        //headers.append('Accept', 'application/json');
        let options = new RequestOptions({ headers: headers });
        this.http.post(url, formData, options)
            .map(res => res.json())
            .catch(error => Observable.throw(error))
            .subscribe(
                data => {
                    if(data.code === 200) {
                      this.p_image = data.link;
                      // this.uploadlink.push(data.link);
                    console.log('file upload success: ' + this.uploadlink);
                    this.errormsg = 'File uploaded successfully';
                    this.buttonflag = true;
                    
                    setTimeout(()=>{  
                            //<<<---    using ()=> syntax
                          this.buttonflag = false;
                     }, 1000);
                    }
                },
                error => {
                    this.errormsg = 'Error';
                    this.buttonflag = true;
                    
                    setTimeout(()=>{  
                            //<<<---    using ()=> syntax
                          this.buttonflag = false;
                     }, 3000);
                    console.log(error)
                }
            )
    }
    }

// TPA utility ends


}


export interface TPAId {
  active: number;
  address: string;
  city: string;
  country: string;
  created_at: string;
  description: string;
  email: string;
  id: number;
  image: string;
  is_broker: string;
  is_insurer: number;
  is_tpa: number;
  name: string;
  state: string;
  tpa_id: number;
  updated_at: string;
}

export interface Ins {
  active: number;
  created_at: string;
  description: string;
  email: string;
  id: number;
  image: string;
  name: string;
  pivot: {
    tpa_id: number;
    insurer_id: number
    };
  insurer_id: number;
  tpa_id: number;
  tat: number;
  updated_at: string;
}

export interface Org {
  active: number;
  address: string;
  city: string;
  country: string;
  created_at: string;
  description: string;
  email: string;
  id: number;
  image: string;
  is_broker: number;
  is_insurer: number;
  is_tpa: number;
  name: string;
  state: string;
  tpa_id: number;
  updated_at: string;
}


