import { Component, OnInit } from '@angular/core';
import { RoleInfo } from '../shared/user-info.service';
import { SendService } from '../shared/send.service';

@Component({
  selector: 'app-mydependents',
  templateUrl: './mydependents.component.html',
  styleUrls: ['./mydependents.component.css']
})
export class MydependentsComponent implements OnInit {
public errormsg = '';
public errorflag = false;

public dependentList: DependentList[] = [];

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
  ) { }

  ngOnInit() {
    // EMP init functions starts
    if (this.roleInfo.isEMP === true) {
      this.sender.getDependentList()
      .subscribe(
          data => {
             console.log(data.code);
             console.log(data.message);
              if (data.code === 200) {
                  
                  this.dependentList = data.data;
                  
                  console.log("Claim regi submitted");
              }else {
                 this.errormsg = data.message;
                 this.errorflag = true;
                 setTimeout(()=>{    //<<<---    using ()=> syntax
                       this.errorflag = false;
                  }, 3000);
              }
     
          },
          error => {
            this.errormsg = error;
            this.errorflag = true;
            setTimeout(()=>{    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
          });
    }// EMP init functions end
  }

  removeDependent (id: number, firstname: string, lastname:string, relation: string) {
    console.log("remove : " +  id);
    this.sender.sendDeletedDependent(id, firstname, lastname, relation)
    .subscribe(
        data => {
           console.log(data.code);
           console.log(data.message);
            if (data.code === 200) {
                this.errormsg = data.message;
                this.errorflag = true;
                setTimeout(()=>{    //<<<---    using ()=> syntax
                      this.errorflag = false;
                 }, 3000);
            }else {
               this.errormsg = data.message;
               this.errorflag = true;
               setTimeout(()=>{    //<<<---    using ()=> syntax
                     this.errorflag = false;
                }, 3000);
            }
   
        },
        error => {
          this.errormsg = error;
          this.errorflag = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
        });
}

}

export interface DependentList {
  policy_name: string;
  policy_number: string;
  id: number;
  user_id: number;
  firstname: string;
  middlename: string;
  lastname: string;
  dob: string;
  relation: string;
  created_at: string;
  updated_at: string;
  gender: string;
  image: string;
  ecard: string;
}



