import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-managecompany',
  templateUrl: './managecompany.component.html',
  styleUrls: ['./managecompany.component.css']
})
export class ManagecompanyComponent implements OnInit {
  public page_id = 1;
  public total_pages = 0;
  public pageCount = [];
  public page_serial = 0;

  public organisationFull: OrganisationFull[] = [];

  public errorflag = false;
  public errormsg = '';
  constructor(
    private roleInfo: RoleInfo,
    private sender: SendService
  ) { }

  ngOnInit() {
    // CSM init starts
    if (this.roleInfo.isCSM === true || this.roleInfo.isBU === true || this.roleInfo.isSA === true) { 
    this.sender.getOrgFull(this.page_id)
    .subscribe(
      data => {
          
          console.log('no Error : ' + data.code);
          if (data.code === 200) {
            
            this.organisationFull = data.data;
            
            this.total_pages = data.total_claims;
            
            this.total_pages = this.total_pages / 10;
            this.total_pages = Math.ceil(this.total_pages);
        
            this.pageCount = [];
            for(var i = 0; i < this.total_pages; i++) {
                this.pageCount.push(1);
            }
            this.errorflag = true;
            this.errormsg = data.message;
            setTimeout(() => {    //<<<---    using ()=> syntax
              this.errorflag = false;
              this.errormsg = '';
         }, 3000);
          }else {
            this.errorflag = true;
            this.errormsg = data.message;
            setTimeout(() => {    //<<<---    using ()=> syntax
              this.errorflag = false;
              this.errormsg = '';
         }, 3000);
          }

      },
      error => {
        this.errorflag = true;
        this.errormsg = error;
        setTimeout(() => {    //<<<---    using ()=> syntax
          this.errorflag = false;
          this.errormsg = '';
     }, 3000);
      });
    }
    // CSM init ends
  }


  nextPage (p_id: number) {
    this.sender.getOrgFull(p_id)
    .subscribe(
      data => {
       
          if (data.code === 200) {
            this.organisationFull = data.data;
              
             
              console.log("New :) pages: " + this.total_pages);
              if (p_id === 1) {
                  this.page_serial = 0;
              }else {
                  this.page_serial = (p_id - 1) * 10; 
              }
          }

      },
      error => {
          console.log('Error : ' + error);
      });
}
}

export interface OrganisationFull {
  organization_id: number;
  organization_name: string;
  tpa_name: string;
  organization_status: number;
}



