import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-mypolicies',
  templateUrl: './mypolicies.component.html',
  styleUrls: ['./mypolicies.component.css']
})
export class MypoliciesComponent implements OnInit {
  public policies: Policies[] = [];
  public sel_policy_index = 0;
  public policy_type = '';

  constructor(
    private roleInfo: RoleInfo,
    private sender: SendService
  ) { }

  ngOnInit() {
    // EMP init functions start
    if (this.roleInfo.isEMP === true) {
    this.sender.getSubdPolicies('emp')
    .subscribe(
      data => {
          
          console.log('no Error : ' + data.code);
          console.log('No error message: ' + data.message);
          if (data.code === 200) {
            
              this.policies = data.data;
              
              console.log('here my emp policy: ' + data.data);
              console.log('subd policies : ' + this.policies);
              if (data.data.enrollment_start_date == null) {
                this.policy_type = 'Employer Provided Policy';
              } else {
                this.policy_type = 'Individually Procured Policy';
              }
          }

      },
      error => {
          console.log('Error : ' + error);
      });
    }// EMP init functions end
  }

}


export interface Policies {
    id: number;
    name: string;
    active: number;
    created_at: string;
    updated_at: string;
    policy_number: string;
    insurer_id: number;
    sum_assured: number;
    benefits: string;
    eligibility: string;
    faqs: string;
    others: string;
    coverage_type: string;
    premium_utilization: string;
    cd_balance: string;
    current_policy_period: string;
    organization_name: string;
    image: string;
    policy_type: string;
}



