import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

import { UserList } from '../shared/users-list.service';
@Component({
  selector: 'app-manageemployees',
  templateUrl: './manageemployees.component.html',
  styleUrls: ['./manageemployees.component.css']
})
export class ManageemployeesComponent implements OnInit {
  public page_id = 1;
  public total_pages = 0;
  public pageCount = [];
  public page_serial = 0;
  public roles: Roles[] = [];

  public errorflag = false;
  public errormsg = '';

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
    private userList: UserList
  ) { }

  ngOnInit() {
    // HR init functions start
    this.sender.getCSMAccList(this.page_id)
    .subscribe(
      data => {
        this.errormsg = data.message;
        setTimeout(()=>{    //<<<---    using ()=> syntax
          this.errorflag = false;
          this.errormsg = '';
         }, 3000);
          if (data.code === 200) {
              this.userList.users = data.data;
              
              this.total_pages = data.total_accounts;
             
              console.log('total count : ' + this.total_pages);
              this.total_pages = this.total_pages / 10;
              this.total_pages = Math.ceil(this.total_pages);
              console.log('page count : ' + this.total_pages);
              console.log("Total pages: " + this.total_pages);
              this.pageCount = [];
              this
              for(var i = 0; i < this.total_pages; i++) {
                  this.pageCount.push(1);
              }
          }

      },
      error => {
          console.log('Error : ' + error);
      });


      this.sender.getRole()
      .subscribe(
        data => {
           
            console.log('no Error role clicker : ' + data.code);
            if (data.code === 200) {
                  this.roles = data.data;
                 
               
            }else {
            
            }
  
        },
        error => {
          
            console.log('Error : ' + error);
        });
    
  
    // HR init functions end
  }

// HR utility starts
userChangeSubmit(edit_f_name: string, edit_l_name: string, edit_role: string, edit_u_id: number, edit_active: number, middlename: string, email: string, other_email: string, mobile: string, gender:string, address: string, city: string, country: string, dob: string) {
  console.log ("user id role -d " + edit_u_id);

  this.sender.sendUserChanges (edit_f_name, edit_l_name, 'emp', edit_u_id, edit_active, middlename, gender, address, city, country, dob)
  .subscribe(
    data => {

        if (data.code === 200) {
            console.log("senduserchanges 200 :" + data.code);
            this.errorflag = true;
            this.errormsg = data.message;
            setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
              this.errormsg = '';
         }, 3000);

        } else {
            console.log("senduserchanges other :" + data.code);
            this.errorflag = true;
            this.errormsg = data.message;
            setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
        }
        
    },
    error => {
        console.log("senduserchanges error :" + error);
        this.errorflag = true;
        this.errormsg = error;
        setTimeout(()=>{    //<<<---    using ()=> syntax
            this.errorflag = false;
       }, 3000);
    });

    
}
// HR utility ends

}


export interface Roles
{  
    id: number;  
    role: string;
    created_at: string;
    updated_at: string;
}


