import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { Router, ActivatedRoute, Params } from '@angular/router';

// import fade in animation
import { fadeInAnimation } from '../_animations/index';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css'],
  // make fade in animation available to this component
animations: [fadeInAnimation],
// attach the fade in animation to the host (root) element of this component
host: { '[@fadeInAnimation]': '' }
})
export class ForgotPasswordComponent implements OnInit {
  public checkflags = false;
  public loadFlag = false;
  public errorflag = false;
  public errormsg = '';
  public trueflag = false;
  public forgotModel: string;
  constructor(
    private sender: SendService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  sendemail() {
    this.loadFlag = true;
   

    this.sender.sendEmail(this.forgotModel)
    .subscribe(
        data => {
            console.log("sendemail-data :" + data.code);
            if (data.code === 200) {
              this.trueflag = true;
              this.loadFlag = false;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                this.trueflag = false;
           }, 3000);
            } else {
              this.errormsg = data.message;
              this.checkflags = true;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.checkflags = false;
               }, 3000);
              this.checkflags = true;
              this.loadFlag = false;
            }

        },
        error => {
          this.errormsg = error;
          this.checkflags = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.checkflags = false;
           }, 3000);
          this.checkflags = true;
          this.loadFlag = false;
            console.log("sendemail-error: " + error);
        });
        

  }

}



