import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { Router } from '@angular/router';
import { RoleInfo } from '../shared/user-info.service';

// import fade in animation
import { fadeInAnimation } from '../_animations/index';

@Component({
  selector: 'app-reset-paswd',
  templateUrl: './reset-paswd.component.html',
  styleUrls: ['./reset-paswd.component.css'],
  // make fade in animation available to this component
  animations: [fadeInAnimation],
  // attach the fade in animation to the host (root) element of this component
  host: { '[@fadeInAnimation]': '' }

})
export class ResetPaswdComponent implements OnInit {

  public paswd: string;
  public conf_paswd: string;
  public errorflag = false;

  constructor(private sender: SendService,
    private router: Router,
    private roleInfo: RoleInfo
  ) { }

  ngOnInit() {
  }

  sendpaswd() {
    if (this.paswd === this.conf_paswd) {
      this.sender.sendPswd(this.paswd)
      .subscribe(
        data => {
          console.log(data.code);
          if (data.code === 200) {
              if (this.roleInfo.firsttime === true){
                this.router.navigate(['/dashboard/profile']);
              } else {
                this.router.navigate(['/dashboard']);
              }
          } else {
             console.log("No 200");
          }

      },
      error => {

          console.log(error);
      });

      }else {
        this.errorflag = true;
      }
    }
}



