import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';
import { ViewChild } from "@angular/core";
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { Router }                       from "@angular/router";
import { FileUtil }                     from './file.util';
import { Constants }                    from './test.constants';
import { environment } from '../../environments/environment.dev';

@Component({
  selector: 'app-tpa-details',
  templateUrl: './tpa-details.component.html',
  styleUrls: ['./tpa-details.component.css']
})



export class TpaDetailsComponent implements OnInit {
  
   @ViewChild('fileImportInput')
   fileImportInput: any;
  
   csvRecords = [];
   public policy_state = 1;
   public errormsg = '';
   public errorflag = false;
   public cor_tpa : TPA[] = [];
  public fields: string[];
  public send_this: any = [];
  public send_tpa_id: number;
  public fileList: FileList;

  rows: number = 21;
   constructor(private _router: Router,
     private _fileUtil: FileUtil,
     private roleInfo: RoleInfo,
     private sender: SendService,
     private http: Http
   ) { }
  
   ngOnInit() { 
    if (this.roleInfo.changePage === 'Keys are mapped.') {
      this.send_tpa_id = this.roleInfo.tpa_id;
      this.policy_state = 2;
      this.errormsg = 'Keys are mapped. Please proceed with file upload.';
      this.errorflag = true;
      setTimeout(()=>{    //<<<---    using ()=> syntax
            this.errorflag = false;
       }, 3000);
    }
    if (this.roleInfo.isCSM === true || this.roleInfo.isBU === true || this.roleInfo.isSA === true) {
      this.sender.getTpaCor () 
      .subscribe(
          data => {
              
              console.log('no Error : ' + data.code);
              if (data.code === 200) {
                this.cor_tpa = data.data;
              }else {
                  this.errormsg = data.message;
                  this.errorflag = true;
                  setTimeout(()=>{    //<<<---    using ()=> syntax
                        this.errorflag = false;
                   }, 3000);
              }
  
          },
          error => {
              console.log('Error : ' + error);
          });
        }
      // CSM init ends
   }
  
   bufferFunc (event) {
    this.fileChangeListener(event);
    this.fileList = event.target.files;
   }


   // METHOD CALLED WHEN CSV FILE IS IMPORTED
   fileChangeListener($event): void {

    
  
     var text = [];
     var files = $event.srcElement.files;
  
     if(Constants.validateHeaderAndRecordLengthFlag){
       if(!this._fileUtil.isCSVFile(files[0])){
         alert("Please import valid .csv file.");
         this.fileReset();
       }
     }
  
    // Upload the file 
   
    // this.fileList = event.target.files;
    //  sessionStorage.setItem('File', JSON.stringify($event.target.files));
    // console.log('File 1 :' + JSON.stringify(this.fileList));
  
    // Upload file ends 
     var input = $event.target;
     var reader = new FileReader();
     reader.readAsText(input.files[0]);
  
     reader.onload = (data) => {
       let csvData = reader.result;
       let csvRecordsArray = csvData.split(/\r\n|\n/);
  
       var headerLength = -1;
       if(Constants.isHeaderPresentFlag){
         let headersRow = this._fileUtil.getHeaderArray(csvRecordsArray, Constants.tokenDelimeter);
         headerLength = headersRow.length; 
         this.send_this.length = headerLength;
         for (var i=0; i <this.send_this.length; i++) {
           this.send_this[i] = true;
         }
         this.fields = headersRow;
         console.log('Headers:::::' + this.fields);
       }
        
       this.csvRecords = this._fileUtil.getDataRecordsArrayFromCSVFile(csvRecordsArray, 
           headerLength, Constants.validateHeaderAndRecordLengthFlag, Constants.tokenDelimeter);
        
       if(this.csvRecords == null){
         //If control reached here it means csv file contains error, reset file.
         this.fileReset();
       }    
     }
  
     reader.onerror = function () {
       alert('Unable to read ' + input.files[0]);
     };
   };
  
   fileReset(){
     this.fileImportInput.nativeElement.value = "";
     this.csvRecords = [];
   }
  


   stateChange(index: number) {
    this.send_tpa_id = this.cor_tpa[index].id;
    this.policy_state = 2;
 }


 verifyFields() {
  let sending_str = '{"tpa_id": ' + this.send_tpa_id + ', "headers":{ '  ;
   
   for (var i = 0; i<this.send_this.length; i++) {
   
    if (this.send_this[i] === true) {
        sending_str += '"' + this.fields[i].toUpperCase().replace(/ /g,"_") + '" : "' + this.fields[i] + '",';
        console.log('Fields to send :: ' + this.fields[i].toUpperCase().replace(/ /g,"_"));
        
      }

    
   }
   sending_str = sending_str.slice(0, -1);
   sending_str += '}}';
   console.log ('sending str : ' + sending_str);

   this.sender.postKeyAdd(sending_str)
   .subscribe(
       data => {
          if (data.code === 200 ){
            this.roleInfo.changePage = '';
            this.errormsg = 'File Verified. Press upload again to Submit';
            this.policy_state = 3;
            this.errorflag = true;
            
            setTimeout(()=>{  
                    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
          }
  
       },
       error => {
        this.roleInfo.changePage = '';
       });
 }

 uploadFile () {
  console.log('File 2 :' + JSON.stringify(this.fileList));
  // this.fileList = JSON.parse(sessionStorage.getItem('File'));
  this.errormsg = 'Uploading File... Please wait.';
  this.errorflag = true;
  // let fileList = event.target.files;
  let url = environment.API_URL + 'claims/tpa/upload ';
  let token = sessionStorage.getItem('Token');
  let n_token = token.replace(/['"]+/g, '');


  
  if(this.fileList.length > 0) {
    console.log('File 3 :' + this.fileList);
      let file: File = this.fileList[0];
      let formData:FormData = new FormData();
      formData.append('tpa_id', <string><any>this.send_tpa_id);
      formData.append('file', file, file.name);
      
      let headers = new Headers();
      /** No need to include Content-Type in Angular 4 */
      //headers.append('Content-Type', 'multipart/form-data');
      headers.append('Authorization', `Bearer ${n_token}`);
      //headers.append('Accept', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.post(url, formData, options)
          .map(res => res.json())
          .catch(error => Observable.throw(error))
          .subscribe(
              data => {
                  if(data.code === 200) {
                   
                  console.log('file upload success: ' + data.code);
                  this.errormsg = 'File uploaded successfully';
                  this.errorflag = true;
                  
                  setTimeout(()=>{  
                          //<<<---    using ()=> syntax
                        this.errorflag = false;
                   }, 3000);
                  }
              },
              error => {
                  this.errormsg = 'Error';
                  this.errorflag = true;
                  
                  setTimeout(()=>{  
                          //<<<---    using ()=> syntax
                        this.errorflag = false;
                   }, 3000);
                  console.log(error)
              }
          )
  }
 }


 fileChange(event) {
  
  this.errormsg = 'Uploading File... Please wait.';
  this.errorflag = true;
let fileList: FileList = event.target.files;
let url = environment.API_URL + 'claims/tpa/upload';
let token = sessionStorage.getItem('Token');
let n_token = token.replace(/['"]+/g, '');



if(fileList.length > 0) {
    let file: File = fileList[0];
    let formData:FormData = new FormData();
    formData.append('tpa_id', <string><any>this.send_tpa_id);
    formData.append('file', file, file.name);
    let headers = new Headers();
    /** No need to include Content-Type in Angular 4 */
    //headers.append('Content-Type', 'multipart/form-data');
    headers.append('Authorization', `Bearer ${n_token}`);
    //headers.append('Accept', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.post(url, formData, options)
        .map(res => res.json())
        .catch(error => Observable.throw(error))
        .subscribe(
            data => {
                if(data.code === 200) {
                  
                console.log('file upload success: ' + data.code);
                this.errormsg = data.message;
                this.errorflag = true;
                
                setTimeout(()=>{  
                        //<<<---    using ()=> syntax
                      this.errorflag = false;
                 }, 3000);
                }
            },
            error => {
                this.errormsg = 'Error';
                this.errorflag = true;
                
                setTimeout(()=>{  
                        //<<<---    using ()=> syntax
                      this.errorflag = false;
                 }, 3000);
                console.log(error)
            }
        )
}
}


goBack() {
  this.policy_state -= 1 ;
}


}
