export interface TPA {
  id: number;
  name: string;
  active: string;
  api: string;
  fields: string;
  created_at: string;
  updated_at: string;
  description: string;
  image: string;
  tat: number;
}

export interface FieldData {
  use: boolean;
  value: string;
}



