import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-editclaims',
  templateUrl: './editclaims.component.html',
  styleUrls: ['./editclaims.component.css']
})
export class EditclaimsComponent implements OnInit {
public errorflag = false;
public errormsg = '';
public singleClaim: SingleClaim[] = [];
public tpaDataFail: boolean;
  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo
  ) { }

  ngOnInit() {
    // TPA init starts
    if (this.roleInfo.isTPA === true || this.roleInfo.isHR === true || this.roleInfo.isCSM || this.roleInfo.isBU) {
        this.errorflag = true;
        this.errormsg = 'Loading data ...';
      let claim_id = this.roleInfo.claimid;
      this.tpaDataFail = false;
      console.log('claim id: ' + claim_id);
      
      this.sender.geteditClaim(claim_id)
      .subscribe(
        data => {
           console.log(data.code);
           console.log(data.message);
            if (data.code === 200) {
               
                this.errorflag = false;
                if (data.data.tpa_data.Message) {
                    this.errorflag = true;
                    this.errormsg = data.data.tpa_data.Message;
                    
                }
                this.singleClaim[0] = data.data;
                //let jsondata = data.data.tpa_data;
                //jsondata = jsondata.slice( 1 );
                
                //jsondata = jsondata.slice(0, -1);
                //this.singleClaim[0].tpa_data = JSON.parse(jsondata);  
  
                this.singleClaim[0].tpa_data = data.data.tpa_data;
                
                  
                
            }else {
  
               this.errormsg = data.message;
               this.errorflag = true;
               setTimeout(()=>{    //<<<---    using ()=> syntax
                     this.errorflag = false;
                }, 3000);
            }
   
        },
        error => {
            console.log('Error : ' + error);
        });
    }
    // TPA init ends 
  }

// TPA utility starts
ClaimChangeSubmit (id: number, stat: string, pat_name: string, rel: string, cl_type: string, cl_amount: number) {
  this.sender.changeClaim(id, stat, pat_name, rel, cl_type, cl_amount)
  .subscribe(
    data => {
       console.log(data.code);
       console.log(data.message);
        if (data.code === 200) {
            console.log("from claims-change: " + data.data);
            this.errormsg = data.message;
            this.errorflag = true;
            setTimeout(()=>{    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
            
           
            
        }else {
           this.errormsg = data.message;
           this.errorflag = true;
           setTimeout(()=>{    //<<<---    using ()=> syntax
                 this.errorflag = false;
            }, 3000);
        }

    },
    error => {
        console.log('Error : ' + error);
    });  
 
}

// TPA utility ends

}



export interface SingleClaim
{
    id: number;
    user_id: number;
    policy_rule: string;
    claim_intimation: string;
    date_of_addmission: string;
    date_of_discharge: string;
    patient_name: string;
    hospital_id: number;
    claimed_amount: number;
    nature_of_illness: string;
    description: string;
    claim_type: string;
    claimid: string;
    employeeid: string;
    tpa_id: number;
    relation: string;
    status: string;
    cheque_amount: number;
    cheque_date: string;
    cheque_number: string;
    policy_id: number;
    attachments: string;
    created_at: string;
    updated_at: string;
    claim_comment: ClaimComment[];
    tpa_data: any;
}

export interface ClaimComment {
    user_id: number;
    id: number;
    claim_id: number;

    comment: string;
    comment_date: string;
    attachment: string;
    created_at: string;
    updated_at: string;
}



