import { Injectable } from '@angular/core';

@Injectable()
export class UserInfo {
    name: string;
    role: string;
    mid_name: string;
    last_name: string;
    cor_email: string;
    gender: string;
    phone: string;
    lastname: string;
    middlename: string;

    roles: Roles[];

    active: number;
    address: string;
    city: string;
    blood_group: string;
    corporate_email_verified: number;
    country: string;
    created_at: string;
    dob: string;
    ecard: string;
    firstname: string;

    id: number;
    image = 'http://www.freelanceme.net/Images/default%20profile%20picture.png';

    mobile: string;
    organization_id: number;
    other_email: string;
    pincode: string;
    remember_token: string;
    state: string;
    updated_at: string;

    business_unit: string;
    department: string;
    designation: string;
    doj: string;
    employee_no: string;
    member_id: string;
    sub_department: string;
    work_location: string;

    allSiteInfo: SiteInfo;

    getRoleInfo () {
        if (sessionStorage.getItem('Token')) {
            if (sessionStorage.getItem('Role') === '"emp"') {
                this.allSiteInfo.isEMP = true;
            }
            if (sessionStorage.getItem('Role') === '"hr"') {
              this.allSiteInfo.isHR = true;
            }
            if (sessionStorage.getItem('Role') === '"csm"') {
              this.allSiteInfo.isCSM = true;
            }
            if (sessionStorage.getItem('Role') === '"tpa"') {
              this.allSiteInfo.isTPA = true;
            }
            if (sessionStorage.getItem('Role') === '"sa"') {
              this.allSiteInfo.isSA = true;
            }
            if (sessionStorage.getItem('Role') === '"bu"') {
              this.allSiteInfo.isBU = true;
            }
        }

    }
}


export interface Roles {
    role: string;
    role_id: number;
    user_id: number;
}


export class Policies {
    prepolicies: PrePolicies[] = [];
}

export interface PrePolicies {
    active: number;
    benefits: string;
    cd_balance: string;
    coverage_type: string;
    created_at: string;
    current_policy_period: string;
    eligibility: string;
    enrollment_end_date: string;
    enrollment_start_date: string;
    faqs: string;
    id: number;
    insurer_id: number;
    name: string;
    others: string;
    policy_end_date: string;
    policy_number: string;
    policy_start_date: string;
    premium_utilization: string;
    sum_assured: string;
    updated_at: string;
}

export interface SingleUserFields
{
    corporate_email: string;
    role_id: number;
    organization_id: number;
}
export interface SiteInfo {
    isEMP: boolean;
    isHR: boolean;
    isCSM: boolean;
    isTPA: boolean;
    isSA: boolean;
    isBU: boolean;
    claimid: number;

    sidestatus: boolean;
}



export class RoleInfo {
    isEMP = false;
    isHR = false;
    isCSM = false;
    isTPA = false;
    isSA = false;
    isBU = false;
    isPA = false;
    claimid = 0;

    sidestatus = false;
    firsttime = false;

    changePage = '';
    tpa_id = 0;
}



