import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';
@Component({
  selector: 'app-manage-tat',
  templateUrl: './manage-tat.component.html',
  styleUrls: ['./manage-tat.component.css']
})
export class ManageTatComponent implements OnInit {
  public errorflag = false;
  public errormsg = '';
  public tatList: TATList[] = [];

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo
  ) { }

  ngOnInit() {

    this.sender.getTATList ()
    .subscribe (
      data => {
        this.tatList = data.data;
        this.errormsg = data.message;
        this.errorflag = true;
        setTimeout(() => {    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);

      },
      error => {
        this.errormsg = error;
        this.errorflag = true;
        setTimeout(() => {    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
      });
  }

  changeBackground(critical: boolean): any {
    // var color='#FE5327';
    var color='#ffa892';
    if (critical === true){
        return { 'background-color': color };
    }
}

sendTATedit (completed_on: string, created_at: string,
  crtical: boolean,
  days_left: number,
  id: number,
  insurer_email: string,
  insurer_name: string,
  organization_email: string,
  organization_id: number,
  organization_name: string,
  responsible: string,
  service: string,
  stage: string,
  started_on: string,
  status: string,
  step: string,
  tat: number,
  tat_rule_id: number,
  tpa_email: string,
  tpa_name: string,
  updated_at: string,
  user_id: number) {

    this.sender.postTATedit(
      completed_on,
      created_at,
      crtical,
      days_left,
      id,
      insurer_email,
      insurer_name,
      organization_email,
      organization_id,
      organization_name,
      responsible,
      service,
      stage,
      started_on,
      status,
      step,
      tat,
      tat_rule_id,
      tpa_email,
      tpa_name,
      updated_at,
      user_id)
      .subscribe(
        data => {},
        error => {}
      );

}

}

export interface TATList {
  completed_on: string;
  created_at: string;
  crtical: boolean;
  days_left: number;
  id: number;
  insurer_email: string;
  insurer_name: string;
  organization_email: string;
  organization_id: number;
  organization_name: string;
  responsible: string;
  service: string;
  stage: string;
  started_on: string;
  status: string;
  step: string;
  tat: number;
  tat_rule_id: number;
  tpa_email: string;
  tpa_name: string;
  updated_at: string;
  user_id: number;
}



