attachMod (str: string) {
   
    if (str != null){
    var comm = str.indexOf(',');
    this.attach_len = str.slice(9, -comm).length;
    return (str.slice(9, -(comm+1)));
    }
}

attachMod2 (str: string) {
    
    if (str != null){
    var len = 9 + this.attach_len + 8;
    return (str.slice(len, -1));
    }
}

