import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/map';
import { environment } from '../../environments/environment.dev';

@Injectable()
export class AuthenticationService {
    constructor(private http: Http) { }
    public url = environment.API_URL + 'login';
    isLoggedIn = false;
    redirectUrl: string;

    login(username: string, password: string, first_time: boolean) {
        let response: any;
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });
        const body = JSON.stringify({email: username, password: password, first_time: first_time});

        return this.http.post(this.url, body, options)
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                let user = response.json();

                return user;
            });
    }

    logout() {
        // remove user from local storage to log user out
        this.isLoggedIn = false;
        localStorage.removeItem('UserToken');
        localStorage.removeItem('LoginData');
        localStorage.setItem('notCounter', '0');
        /*<HTMLInputElement>document.getElementById('notif_count')).value = '0';*/
    }

    
}


