import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

import { Router } from '@angular/router';

@Component({
  selector: 'app-managepolicies',
  templateUrl: './managepolicies.component.html',
  styleUrls: ['./managepolicies.component.css']
})
export class ManagepoliciesComponent implements OnInit {
  public policies: Policies[] = [];
  public sel_policy_index = 0;
  public tpaID: TPAId;
  public policy_type= '';
  public errorflag = false;
  public errormsg = '';

  constructor(
    private roleInfo: RoleInfo,
    private sender: SendService,
    private router: Router
  ) { }

  ngOnInit() {
    // HR init start 
    if (this.roleInfo.isHR === true) {
      this.sender.getPolicies()
      .subscribe(
        data => {
            
            console.log('no Error : ' + data.code);
            if (data.code === 200) {
                this.policies = data.data;
                console.log('here poliiiiiiii: ' + data);
                console.log('policies : ' + this.policies);
            }
            if (data.data.enrollment_start_date === null) {
              this.policy_type = 'Employer Provided Policy';
            } else {
              this.policy_type = 'Individually Procured Policy';
            }
  
        },
        error => {
            console.log('Error : ' + error);
        });
  
    }// HR init ends

    // TPA init starts
    if (this.roleInfo.isSA === true || this.roleInfo.isCSM === true || this.roleInfo.isBU === true) {
      this.sender.getPolicies()
      .subscribe(
        data => {
            
            console.log('no Error : ' + data.code);
            if (data.code === 200) {
                this.policies = data.data;
                console.log('here poliiiiiiii: ' + data);
                console.log('policies : ' + this.policies);
            }
            if (data.data.enrollment_start_date == null) {
              this.policy_type = 'Employer Provided Policy';
            } else {
              this.policy_type = 'Individually Procured Policy';
            }
  
        },
        error => {
            console.log('Error : ' + error);
        });
  
    }
    // TPA init ends
  }
// TPA utility starts
  editPolicy () {
    this.sender.getTPAId()
    .subscribe(
        data => {
          if (data.code === 200) {
            this.tpaID = data.data;
          }
        },
        error => {

        });
        this.router.navigate(['/dashboard/manage-policies']);
        this.errorflag = true;
        this.errormsg = 'Changes Submitted';
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
  }
// TPA utility ends
}


export interface Policies {
    id: number;
    name: string;
    active: number;
    created_at: string;
    updated_at: string;
    policy_number: string;
    insurer_id: number;
    sum_assured: number;
    benefits: string;
    eligibility: string;
    faqs: string;
    others: string;
    coverage_type: string;
    premium_utilization: string;
    cd_balance: string;
    current_policy_period: string;
    organization_name: string;
    image: string;
    policy_type: string;
}

export interface TPAId {
  active: number;
  address: string;
  city: string;
  country: string;
  created_at: string;
  description: string;
  email: string;
  id: number;
  image: string;
  is_broker: string;
  is_insurer: number;
  is_tpa: number;
  name: string;
  state: string;
  tpa_id: number;
  updated_at: string;
}



