import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-right-sidebar',
  templateUrl: './right-sidebar.component.html',
  styleUrls: ['./right-sidebar.component.css']
})
export class RightSidebarComponent implements OnInit {
  public errormsg = '';
  public errorflag = false;

  public general: General[] = [];
  public daily: Daily = null;
  public newsletter: Newsletter = null;
  public wellness: Wellness = null;

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo
  ) { }

  ngOnInit() {
    this.sender.getContent()
    .subscribe(
      data => {
        if (data.code === 200) {
         
          if (data.general !== null) {
            this.general = data.general;

          }
          if (data.daily !== null) {
            this.daily = data.daily;
          }
          if (data.newsletter !== null) {
            this.newsletter = data.newsletter;
          }
          if (data.wellness_magazine !== null) {
            this.wellness = data.wellness_magazine;

          }
        
        }else {
          this.errormsg = data.message;
          this.errorflag = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
        }

      },
      error => {}
    );
  }

}

export interface General {
  added_on: string;
  created_at: string;
  description: string;
  id: number;
  link: string;
  name: string;
  type: string;
  updated_at: string;
  user_id: number;
}

export interface Daily {
  added_on: string;
  created_at: string;
  description: string;
  id: number;
  link: string;
  name: string;
  type: string;
  updated_at: string;
  user_id: number;
}

export interface Newsletter {
  added_on: string;
  created_at: string;
  description: string;
  id: number;
  link: string;
  name: string;
  type: string;
  updated_at: string;
  user_id: number;
}

export interface Wellness {
  added_on: string;
  created_at: string;
  description: string;
  id: number;
  link: string;
  name: string;
  type: string;
  updated_at: string;
  user_id: number;
}


