    claimid: string;
    employeeid: string;
    tpa_id: number;
    relation: string;
    status: string;
    cheque_amount: number;
    cheque_date: string;
    cheque_number: string;
    policy_id: number;
    attachments: string;
    created_at: string;
    updated_at: string;
    critical: boolean;
