import { Component, OnInit } from '@angular/core';
import { AppGlobals } from '../shared/globalflags.service';
import { AuthenticationService } from '../shared/authentication.service';
import { SendService } from '../shared/send.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Http } from '@angular/http';

import { User } from '../user';
import { RoleInfo, UserInfo, Policies} from '../shared/user-info.service';
import { ContainerInfo } from '../right-container/right-container.service';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.css']
})
export class LoginPageComponent implements OnInit {
public isLoggedIn: boolean;
public isSA: boolean = false;
public isCSM: boolean = false;
public forgot: boolean = false;
public checkflags: boolean = false;
public loadFlag: boolean = false;
public otp: boolean = false;
public reset: boolean = false;
public log = true;
public token:string ;
public id:number;
public trueflag:boolean=false;
public time_check: string;
public url: string = "52.11.67.218/api/v1/login";
public errormsg= '';



model = new User('', '', false, false);

forgotModel: string;

  constructor(
              private roleInfo: RoleInfo,
              private _appGlobals: AppGlobals,
              private router: Router,
              private http: Http,
              private authenticationService: AuthenticationService,
              private userInfo: UserInfo,
              private pre_policies: Policies,
              private sender: SendService,
              private active: ActivatedRoute,
              private rightContainer: ContainerInfo) {
  this._appGlobals.isUserLoggedIn.subscribe(value => this.isLoggedIn = value);

}






  loginActivitySA() {
    this.isLoggedIn = true;
    this.isSA = true;
    this.isCSM = false;
    this._appGlobals.setLoginStatus(this.isLoggedIn);
    this._appGlobals.setCSMStatus(this.isCSM);
    this._appGlobals.setSAStatus(this.isSA);
    console.log('login-login:' + this.isLoggedIn);
    this.router.navigate(['/home']);
}

  loginActivityCSM() {
    this.isLoggedIn = true;
    this.isCSM = true;
    this.isSA = false;
    this._appGlobals.setLoginStatus(this.isLoggedIn);
    this._appGlobals.setCSMStatus(this.isCSM); 
    this._appGlobals.setSAStatus(this.isSA);

    this.router.navigate(["/home"]);
}

  ngOnInit() {
    sessionStorage.setItem('Flag', '0');
    if(this._appGlobals.user_change === true) {
      if(this._appGlobals.user_type === 'hr') {
          this.roleInfo.isEMP = false;
          this.roleInfo.isHR = true;
          this.router.navigate(['/dashboard']);
        }
      if(this._appGlobals.user_type === 'emp') {
        this.roleInfo.isHR = false;
        this.roleInfo.isEMP = true;
        this.router.navigate(['/dashboard']);
      }
    }
    
    
    this.active.queryParams.subscribe((params: Params) => {
        this.time_check = params['function'];

    });
  }

forgotPass () {
  this.checkflags = false;
  this.loadFlag = false;
  this.log = false;
  this.forgot = true;
  this.otp = false;
  this.reset = false;

}

unforget () {
  this.checkflags = false;
  this.loadFlag = false;
  this.forgot = false;
  this.log = true;
  this.otp = false;
  this.reset = false;
}


submitted = false;

  onSubmit() {
    this.loadFlag = true;
    this.submitted = true;
    if (this.time_check === 'first_time') {
        this.model.first_time = true;
    }
    this.authenticationService.login(this.model.name, this.model.password, this.model.first_time)
    .subscribe(
        data => {
            console.log(data);
            if (data.status_code === 200) {
              this.isLoggedIn = true;
              this.isSA = false;
              this.isCSM = true;
              this._appGlobals.setLoginStatus(this.isLoggedIn);
              this._appGlobals.setCSMStatus(this.isCSM);
              this._appGlobals.setSAStatus(this.isSA);
              this.loadFlag = false;
              this.userInfo.name = data.user.firstname;
              this.userInfo.role = data.user_roles[0].role;
              sessionStorage.setItem('Role', JSON.stringify(data.user_roles[0].role));
              
              this.userInfo.mid_name = data.user.middlename;
              this.userInfo.last_name = data.user.lastname;
              this.userInfo.cor_email = data.user.corporate_email;
              this.userInfo.gender = data.user.gender;
              this.userInfo.phone = data.user.mobile;
              this.userInfo.ecard = data.user.ecard;
              this.userInfo.address = data.user.address;
              this.userInfo.dob = data.user.dob;
              this.userInfo.roles = data.user_roles;
              if(data.user.image) {
              this.userInfo.image = data.user.image;
              }

              if(data.employee) {
              this.userInfo.business_unit = data.employee.business_unit;
              this.userInfo.department = data.employee.department;
              this.userInfo.designation = data.employee.designation;
              this.userInfo.doj = data.employee.doj;
              this.userInfo.employee_no = data.employee.employee_no;
              this.userInfo.member_id = data.employee.member_id;
              this.userInfo.sub_department = data.employee.sub_department;
              this.userInfo.work_location = data.employee.work_location;
              }
              if(data.pre_enrollment === true) {
                this.model.pre_enrollment = true;
                this.pre_policies.prepolicies = data.policies;
                //localStorage.setItem('PrePolicy', res.json()[data.policies]);
                console.log('login pre-policy: ' + this.pre_policies.prepolicies);
              }

              if(this.model.first_time === true) {
                this.rightContainer.profile = true;
                this.rightContainer.dashboard = false;
                this.rightContainer.create = false;
                this.rightContainer.edit = false;
                this.rightContainer.manage = false;
                this.rightContainer.first_time = true;
                this.router.navigate(['/home']);
              } 
              if (this.model.pre_enrollment === true) {
                this.rightContainer.pre_enroll = true;
                this.router.navigate(['/home']);
              }
              else {
                
                this.router.navigate(['/home']);
              }
            } else {
              this.loadFlag = false;
              this.checkflags = true;

              this.errormsg = data.message;
              this.checkflags = true;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.checkflags = false;
               }, 3000);
            }

        },
        error => {
          this.errormsg = error;
          this.checkflags = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.checkflags = false;
           }, 3000);
            console.log(error);
        });


  }

  onLogin() {
    this.loadFlag = true;
    this.authenticationService.login(this.model.name, this.model.password, this.model.first_time)
    .subscribe(
        data => {
            if (data.status_code === 200) {
              sessionStorage.setItem('IsLoggedIn', '1');
              this.loadFlag = false;
              sessionStorage.setItem('Role', JSON.stringify(data.user_roles[0].role));
              sessionStorage.setItem('Token', JSON.stringify(data.access_token));
              if (data.user_roles[1]) {
                sessionStorage.setItem('NextToken', JSON.stringify(data.user_roles[1].role));
              }
              this.userInfo.name = data.user.firstname;
              this.userInfo.role = data.user_roles[0].role;
              this.userInfo.mid_name = data.user.middlename;
              this.userInfo.last_name = data.user.lastname;
              this.userInfo.cor_email = data.user.corporate_email;
              this.userInfo.gender = data.user.gender;
              this.userInfo.phone = data.user.mobile;
              this.userInfo.ecard = data.user.ecard;
              this.userInfo.blood_group = data.user.blood_group;
              this.userInfo.address = data.user.address;
              this.userInfo.dob = data.user.dob;
              this.userInfo.roles = data.user_roles;
              if(data.user.image) {
              this.userInfo.image = data.user.image;
              }else{
                this.userInfo.image = 'http://www.freelanceme.net/Images/default%20profile%20picture.png';
              }

              if(data.employee) {
              this.userInfo.business_unit = data.employee.business_unit;
              this.userInfo.department = data.employee.department;
              this.userInfo.designation = data.employee.designation;
              this.userInfo.doj = data.employee.doj;
              this.userInfo.employee_no = data.employee.employee_no;
              this.userInfo.member_id = data.employee.member_id;
              this.userInfo.sub_department = data.employee.sub_department;
              this.userInfo.work_location = data.employee.work_location;
              }
              if (sessionStorage.getItem('Token')) {
                let role = sessionStorage.getItem('Role');
                if (sessionStorage.getItem('Role') === '"emp"') {
                 this.roleInfo.isEMP = true;
                }
                if (sessionStorage.getItem('Role') === '"hr"') {
                  this.roleInfo.isHR = true;
                }
                if (sessionStorage.getItem('Role') === '"csm"') {
                  this.roleInfo.isCSM = true;
                }
                if (sessionStorage.getItem('Role') === '"tpa"') {
                  this.roleInfo.isTPA = true;
                }
                if (sessionStorage.getItem('Role') === '"sa"') {
                  this.roleInfo.isSA = true;
                }
                if (sessionStorage.getItem('Role') === '"bu"') {
                  this.roleInfo.isBU = true;
                }
                if (sessionStorage.getItem('Role') === '"pa"') {
                  this.roleInfo.isPA = true;
                }
                if (this.time_check === 'first_time') {
                  this.roleInfo.firsttime = true;
                  this.router.navigate(['/reset']);
                }else {
                  this.router.navigate(['/dashboard']);
                }
              }
            }else 
            {
              this.loadFlag = false;
              this.checkflags = true;

              this.errormsg = data.message;
              this.checkflags = true;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.checkflags = false;
               }, 3000);
            }
          },
        error => {
          this.errormsg = error;
          this.checkflags = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.checkflags = false;
           }, 3000);
            console.log(error);
        });


  }


  sendemail() {
    this.loadFlag = true;
   

    this.sender.sendEmail(this.forgotModel)
    .subscribe(
        data => {
            console.log("sendemail-data :" + data.code);
            if (data.code === 200) {
              this.trueflag = true;
              this.loadFlag = false;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                this.trueflag = false;
           }, 3000);
            } else {
              this.errormsg = data.message;
              this.checkflags = true;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.checkflags = false;
               }, 3000);
              this.checkflags = true;
              this.loadFlag = false;
            }

        },
        error => {
          this.errormsg = error;
          this.checkflags = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.checkflags = false;
           }, 3000);
          this.checkflags = true;
          this.loadFlag = false;
            console.log("sendemail-error: " + error);
        });
        

  }


  toOtp() {
    this.log = false;
    this.forgot = false;
    this.otp = true;
    this.reset = false;
  }

  toReset() {
    this.log = false;
    this.forgot = false;
    this.otp = false;
    this.reset = true;
  }

}



