import { Injectable } from '@angular/core';

@Injectable()
export class UserList {
  public users: Users[] = [];
}


export interface Users {
  active: number;
  address: string;
  blood_group: string;
  city: string;
  corporate_email: string;
  corporate_email_verified: number;
  country: string;
  created_at: string;
  description: string;
  dob: string;
  ecard: string;
  email: string;
  firstname: string;
  gender: string;
  id: number;
  image: string;
  is_broker: number;
  is_insurer: string;
  is_tpa: string;
  lastname: string;
  middlename: string;
  mobile: string;
  name: string;
  organization_id: number;
  other_email: string;
  password: string;
  pincode: string;
  remember_token: string;
  role: string;
  role_id: string;
  state: string;
  tpa_id: number;
  updated_at: string;
  user_id: number;
}





