import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

import { UserList } from '../shared/users-list.service';


@Component({
  selector: 'app-manageusers',
  templateUrl: './manageusers.component.html',
  styleUrls: ['./manageusers.component.css'],

})
export class ManageusersComponent implements OnInit {
  public page_id = 1;
  public total_pages = 0;
  public pageCount = [];
  public page_serial = 0;
    public roles: Roles[] = [];
  public errorflag = false;
  public errormsg = '';
  public role_id: number;
  public filter = '/none';
  public allOrgs: AllOrgs[] = [];
  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
    private userList: UserList
  ) { }

  ngOnInit() {
      this.sender.getOrg()
      .subscribe (
          data => {
              if (data.code === 200) {
                  this.allOrgs = data.data;
              }
          },
          error => {}
      );
    // CSM init start
    if (this.roleInfo.isCSM === true || this.roleInfo.isBU === true || this.roleInfo.isSA === true){
    this.sender.getAccList(this.page_id, this.filter)
    .subscribe(
      data => {
          console.log("reaching here -------------------");
          if (data.code === 200) {
              this.userList.users = data.data;
              
              this.total_pages = data.total_accounts;
              console.log('total count : ' + this.total_pages);
              this.total_pages = this.total_pages / 10;
              this.total_pages = Math.ceil(this.total_pages);
              console.log('page count : ' + this.total_pages);
              console.log("Total pages: " + this.total_pages);
              this.pageCount = [];
              for(var i = 0; i < this.total_pages; i++) {
                  this.pageCount.push(1);
              }
          }

      },
      error => {
          console.log('Error : ' + error);
      });

      this.sender.getRole()
      .subscribe (
            data => {
                if (data.code === 200) {
                    this.roles = data.data;
                }
            },
            error => {}
      );
    }
    // CSM init end
  }

  boxClick() {
	$(document).ready(function() {
        var activeSystemClass = $('.list-group-item.active');
		
        //something is entered in search form
        $('#system-search').keyup( function() {
			
           var that = this;
            // affect all table rows on in systems table
            var tableBody = $('.table-list-search tbody');
            var tableRowsClass = $('.table-list-search tbody tr');
            $('.search-sf').remove();
            tableRowsClass.each( function(i, val) {

                //Lower text for case insensitive
                var rowText =  $(val).text().toLowerCase();
                var inputText =  $(that).val().toString().toLowerCase();
                if(inputText != '')
                {
                    $('.search-query-sf').remove();
                    tableBody.prepend('<tr class="search-query-sf"><td colspan="6"><strong>Searching for: "'
                        + $(that).val()
                        + '"</strong></td></tr>');
                }
                else
                {
                    $('.search-query-sf').remove();
                }

                if( rowText.indexOf( inputText ) == -1 )
                {
                    //hide rows
                    tableRowsClass.eq(i).hide();

                }
                else
                {
                    $('.search-sf').remove();
                    tableRowsClass.eq(i).show();
                }
            });
            //all tr elements are hidden
            if(tableRowsClass.children(':visible').length == 0)
            {
                tableBody.append('<tr class="search-sf"><td class="text-muted" colspan="6">No entries found.</td></tr>');
            }
        });
        });
			
		
  }





  // CSM utility starts

  getAccOrgList (org_id: number) {
      console.log('Org============' + org_id);
      this.filter = '/' + org_id.toString();
    // this.filter = '/organization';
    this.getListAgain();
  }

  getAccAllList() {
      console.log('all============');
      this.filter = '/none';
      this.getListAgain();
  }

  getListAgain(){
    this.sender.getAccList(this.page_id, this.filter)
    .subscribe(
      data => {
          console.log("reaching here -------------------");
          if (data.code === 200) {
              this.userList.users = data.data;
              
              this.total_pages = data.total_accounts;
              console.log('total count : ' + this.total_pages);
              this.total_pages = this.total_pages / 10;
              this.total_pages = Math.ceil(this.total_pages);
              console.log('page count : ' + this.total_pages);
              console.log("Total pages: " + this.total_pages);
              this.pageCount = [];
              for(var i = 0; i < this.total_pages; i++) {
                  this.pageCount.push(1);
              }
          }
          else {
            this.errormsg = data.message;
            this.errorflag = true;
            setTimeout(()=>{    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
          }

      },
      error => {
          console.log('Error : ' + error);
      });
  }

  nextPageCSMUser (p_id: number, filter: string) {
  
      this.sender.getAccList(p_id, filter)
      .subscribe(
        data => {
         
            if (data.code === 200) {
                this.userList.users = data.data;
                
               
                console.log("New :) pages: " + this.total_pages);
                if (p_id === 1) {
                    this.page_serial = 0;
                }else {
                    
                    this.page_serial = (p_id - 1) * 10; 
                }
            }
  
        },
        error => {
            this.errorflag = true;
            this.errormsg = error;
            setTimeout(()=>{    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
            console.log('Error : ' + error);
        });
  }

  userChangeSubmit(edit_f_name: string, edit_l_name: string, edit_role: string, edit_u_id: number, edit_active: number, middlename: string, email: string, other_email: string, mobile: string, gender:string, address: string, city: string, country: string, dob: string) {
    console.log ("user id role -d " + edit_u_id);
    this.sender.sendUserChanges (edit_f_name, edit_l_name, edit_role, edit_u_id, edit_active, middlename, gender, address, city, country, dob)
    .subscribe(
      data => {
  
          if (data.code === 200) {
              console.log("senduserchanges 200 :" + data.code);
              this.errorflag = true;
              this.errormsg = data.message;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
                this.errormsg = '';
           }, 3000);
  
          } else {
              console.log("senduserchanges other :" + data.code);
              this.errorflag = true;
              this.errormsg = data.message;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
          }
          
      },
      error => {
          console.log("senduserchanges error :" + error);
          this.errorflag = true;
          this.errormsg = error;
          setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
      });
  
    }
  // CSM utility ends
}


export interface Roles  
{  
    id: number;  
    role: string;
    created_at: string;
    updated_at: string;
}

export interface AllOrgs {
    id: number;
    name: string;
}



