import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginPageComponent } from './login-page/login-page.component';
import { HomePageComponent } from './home-page/home-page.component';
import { OtpComponent } from './otp/otp.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { AppComponent } from './app.component';
import { ResetPaswdComponent } from './reset-paswd/reset-paswd.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AddclaimsComponent } from './addclaims/addclaims.component';
import { MaincontentComponent } from './maincontent/maincontent.component';
import { ManageclaimsComponent } from './manageclaims/manageclaims.component';
import { MypoliciesComponent } from './mypolicies/mypolicies.component';
import { PolicycenterComponent } from './policycenter/policycenter.component';
import { MydependentsComponent } from './mydependents/mydependents.component';
import { AdddependentsComponent } from './adddependents/adddependents.component';
import { EditdependentComponent } from './editdependent/editdependent.component';
import { ProfileComponent } from './profile/profile.component';
import { AdduserComponent } from './adduser/adduser.component';
import { ManagerequestsComponent } from './managerequests/managerequests.component';
import { ManageemployeesComponent } from './manageemployees/manageemployees.component';
import { ManagepoliciesComponent } from './managepolicies/managepolicies.component';
import { AddemployeeComponent } from './addemployee/addemployee.component';
import { EditclaimsComponent } from './editclaims/editclaims.component';
import { ManageusersComponent } from './manageusers/manageusers.component';
import { AddpoliciesComponent } from './addpolicies/addpolicies.component';
import { AddcompanyComponent } from './addcompany/addcompany.component';
import { ManagecompanyComponent } from './managecompany/managecompany.component';
import { ManageservicerequestComponent } from './manageservicerequest/manageservicerequest.component';
import { AddtpaComponent } from './addtpa/addtpa.component';
import { ManagecsmComponent } from './managecsm/managecsm.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { ManageTatComponent } from './manage-tat/manage-tat.component';
import { AddTatComponent } from './add-tat/add-tat.component';
import { DeleteUsersComponent } from './delete-users/delete-users.component';
import { TpaDetailsComponent } from './tpa-details/tpa-details.component';
import { AddContentComponent } from './add-content/add-content.component';
import { AddTpaApiComponent } from './add-tpa-api/add-tpa-api.component';
import { NearbyHospitalsComponent } from './nearby-hospitals/nearby-hospitals.component';
import { ReportCenterComponent } from './report-center/report-center.component';
import { UpdateClaimsComponent } from './update-claims/update-claims.component';
import { PremiumUtilisationComponent } from './premium-utilisation/premium-utilisation.component';
import { AuthGuard } from './shared/auth-guard.service';

const appRoutes: Routes = [
  {path: '', redirectTo: 'login', pathMatch: 'full'},
  { path: 'login', component: LoginPageComponent},
  { path: 'forgot-password', component: ForgotPasswordComponent},
  { path: 'home', component: HomePageComponent, canActivate: [AuthGuard], },
  {path: 'otp/:token/:id', component: OtpComponent},
  {path: 'reset', component: ResetPaswdComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard],
  children: [
    {path: 'addclaims',
    component: AddclaimsComponent},
    {
      path: 'manageclaims',
      component: ManageclaimsComponent
    },
    {
      path: 'mypolicies',
      component: MypoliciesComponent
    },
    {
      path: 'policycenter',
      component: PolicycenterComponent
    },
    {
      path: 'mydependents',
      component: MydependentsComponent
    },
    {
      path: 'adddependents',
      component: AdddependentsComponent
    },
    {
      path: 'editdependent',
      component: EditdependentComponent
    },
    {
      path: 'profile',
      component: ProfileComponent
    },
    {
      path: 'add-user',
      component: AdduserComponent
    },
    {
      path: 'manage-requests',
      component: ManagerequestsComponent
    },
    {
      path: 'manage-employees',
      component: ManageemployeesComponent
    },
    {
      path: 'add-employees',
      component: AddemployeeComponent
    },
    {
      path: 'manage-policies',
      component: ManagepoliciesComponent
    },
    {
      path: 'edit-claims',
      component: EditclaimsComponent
    },
    {
      path: 'manage-users',
      component: ManageusersComponent
    },
    {
      path: 'add-policy',
      component: AddpoliciesComponent
    },
    {
      path: 'add-company',
      component: AddcompanyComponent
    },
    {
      path: 'manage-company',
      component: ManagecompanyComponent
    },
    {
      path: 'manage-service-request',
      component: ManageservicerequestComponent
    },
    {
      path: 'add-tpa/insurer',
      component: AddtpaComponent
    },
    {
      path: 'manage-csm',
      component: ManagecsmComponent
    },
    {
      path: 'manage-tat',
      component: ManageTatComponent
    },
    {
      path: 'delete-users',
      component: DeleteUsersComponent
    },
    {
      path: 'add-tat',
      component: AddTatComponent
    },
    {
      path: 'add-content',
      component: AddContentComponent
    },
    {
      path: 'tpa-details',
      component: TpaDetailsComponent
    },
    {
      path: 'nearby-hospitals',
      component: NearbyHospitalsComponent
    },
    {
      path: 'add-tpa-api',
      component: AddTpaApiComponent
    },
    {
      path: 'reports',
      component: ReportCenterComponent
    },
    {
      path: 'update-claims',
      component: UpdateClaimsComponent
    },
    {
      path: 'premium-util',
      component: PremiumUtilisationComponent
    },
    {
      path: '',
      component: MaincontentComponent
    }
  ]
 },
  { path: '**', component: PageNotFoundComponent },


  	/*children: [
      {
        path: 'create'
        component: ''
      },
      {
        path: 'edit'
      }
    ]
   },*/

];

@NgModule({
  imports: [
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: false } // <-- debugging purposes only
    )
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule {}



