import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';
import { ViewChild } from "@angular/core";
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { Router } from "@angular/router";
import { FileUtil } from '../tpa-details/file.util';
import { Constants } from '../tpa-details/test.constants';
import { JquerySortableDirective } from './jquery-sortable.directive';
import * as $ from 'jquery';

@Component({
  selector: 'app-update-claims',
  templateUrl: './update-claims.component.html',
  styleUrls: ['./update-claims.component.css']
})
export class UpdateClaimsComponent implements OnInit {
  @ViewChild('fileImportInput')
  fileImportInput: any;
 
  csvRecords = [];
  public policy_state = 1;
  public errormsg = '';
  public errorflag = false;
  public cor_tpa : TPA[] = [];
 public fields: string[];
 public send_this: any = [];
 public send_tpa_id: number;
 public fileList: FileList;
 public defKeys: string[];

  constructor(
    private _router: Router,
    private _fileUtil: FileUtil,
    private roleInfo: RoleInfo,
    private sender: SendService,
    private http: Http
  ) { }

  ngOnInit() {
    if (this.roleInfo.isCSM === true || this.roleInfo.isBU === true || this.roleInfo.isSA === true) {
      this.sender.getTpaCor () 
      .subscribe(
          data => {
              
              console.log('no Error : ' + data.code);
              if (data.code === 200) {
                this.cor_tpa = data.data;
              }else {
                  this.errormsg = data.message;
                  this.errorflag = true;
                  setTimeout(()=>{    //<<<---    using ()=> syntax
                        this.errorflag = false;
                   }, 3000);
              }
  
          },
          error => {
              console.log('Error : ' + error);
          });
        }
  }

  stateChange(index: number) {
    this.send_tpa_id = this.cor_tpa[index].id;
    this.policy_state = 4;
    
    }


 stateChangeagain() {
  this.sender.postTPAcheck ('manual', this.send_tpa_id)
  .subscribe (
    data => {
      if (data.code === 200) {
        if (data.data.mapping_exists === true) {
          this.roleInfo.changePage = 'Keys are mapped.';
          this.roleInfo.tpa_id = this.send_tpa_id;
          this._router.navigate (['dashboard/tpa-details']);
          }
        if (data.data.mapping_exists === false) {
          this.errormsg = 'Keys not mapped.\n Please upload file and map keys with default keys.';
          this.errorflag = true;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
          this.policy_state = 2;
        }
      }
    },
    error => {}
  );

  }


  stateChangeapi() {
    this.sender.postTPAcheck ('api', this.send_tpa_id)
    .subscribe (
      data => {
        if (data.code === 200) {
          if (data.data.mapping_exists === true) {
            this.errormsg = 'Keys are already mapped.';
            this.errorflag = true;
            setTimeout(()=>{    //<<<---    using ()=> syntax
                  this.errorflag = false;
             }, 3000);
            this.policy_state = 3;
            }
          if (data.data.mapping_exists === false) {
            this.roleInfo.changePage = 'Keys are not mapped.';
            this.roleInfo.tpa_id = this.send_tpa_id;
           
            this._router.navigate(['dashboard/add-tpa-api']);
          }
        }
      },
      error => {}
    );
  
    }

 bufferFunc (event) {
  this.sender.getKeys()
  .subscribe (
    data => {
      if (data.code === 200) {
        let buffstr: any;
        buffstr = data.data.data.slice(1, -1);
        this.defKeys = buffstr.split(',');
        for(var i=0; i<this.defKeys.length; i++) {
          this.defKeys[i] = this.defKeys[i].slice(1, -1);
        }

        console.log('buf str :::::' + buffstr + this.defKeys);
      }
    },
    error => {}
  );

  this.fileChangeListener(event);
  this.fileList = event.target.files;
 }


 // METHOD CALLED WHEN CSV FILE IS IMPORTED
 fileChangeListener($event): void {

  

   var text = [];
   var files = $event.srcElement.files;

   if(Constants.validateHeaderAndRecordLengthFlag){
     if(!this._fileUtil.isCSVFile(files[0])){
       alert("Please import valid .csv file.");
       this.fileReset();
     }
   }

  // Upload the file 
 
  // this.fileList = event.target.files;
  //  sessionStorage.setItem('File', JSON.stringify($event.target.files));
  // console.log('File 1 :' + JSON.stringify(this.fileList));

  // Upload file ends 
   var input = $event.target;
   var reader = new FileReader();
   reader.readAsText(input.files[0]);

   reader.onload = (data) => {
     let csvData = reader.result;
     let csvRecordsArray = csvData.split(/\r\n|\n/);

     var headerLength = -1;
     if(Constants.isHeaderPresentFlag){
       let headersRow = this._fileUtil.getHeaderArray(csvRecordsArray, Constants.tokenDelimeter);
       headerLength = headersRow.length; 
       this.send_this.length = headerLength;
       for (var i=0; i <this.send_this.length; i++) {
         this.send_this[i] = true;
       }
       this.fields = headersRow;
       console.log('Headers:::::' + this.fields);
     }
      
     this.csvRecords = this._fileUtil.getDataRecordsArrayFromCSVFile(csvRecordsArray, 
         headerLength, Constants.validateHeaderAndRecordLengthFlag, Constants.tokenDelimeter);
      
     if(this.csvRecords == null){
       //If control reached here it means csv file contains error, reset file.
       this.fileReset();
     }    
   }

   reader.onerror = function () {
     alert('Unable to read ' + input.files[0]);
   };
 }
 

 fileReset(){
  this.fileImportInput.nativeElement.value = "";
  this.csvRecords = [];
}

verifyFields() {
  console.log('sorted ================ : ' + this.fields);
  let sendstr = '{';
  for (var i=0; i<this.fields.length; i++) {
    sendstr += '\"' + this.defKeys[i] + '\":\"' + this.fields[i] + '\", ';
  }
  sendstr = sendstr.slice(0, -1);
  sendstr = sendstr.slice(0, -1);
  sendstr += '}';
  sendstr = JSON.stringify(sendstr);
  console.log('senstr ++++++++++++++' + sendstr + this.send_tpa_id);
  this.sender.postMatchedKeys(sendstr, this.send_tpa_id)
  .subscribe (
    data => {
      this.errormsg = data.message;
      this.errorflag = true;
      setTimeout(()=>{    //<<<---    using ()=> syntax
            this.errorflag = false;
       }, 3000);
    },
    error => {}
  )
}
 
goBack (page_id: number ){
  this.policy_state = page_id;
}

updateInitiate () {
  this.sender.postUpdateInitiate(this.send_tpa_id)
  .subscribe (
    data => {
      this.errormsg = data.message;
      this.errorflag = true;
      setTimeout(()=>{    //<<<---    using ()=> syntax
            this.errorflag = false;
       }, 3000);
    },
    error => {}
  );
}


} 


export interface TPA {
  id: number;
  name: string;
  active: string;
  api: string;
  fields: string;
  created_at: string;
  updated_at: string;
  description: string;
  image: string;
  tat: number;
}



