import { Injectable } from '@angular/core';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/share';
import 'rxjs/add/operator/startWith';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class AppGlobals {
public userRole: BehaviorSubject<string> = new BehaviorSubject<string>('hr');
public isSa: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
public isCSM: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(true);
public display_profile: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
public user_change = false;
public user_type = 'hr';
// use this property for property binding
  public isUserLoggedIn: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  public isForgotPassword: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(true);


// Observables
  userRole$ = this.userRole.asObservable();
  loginStatus$ = this.isUserLoggedIn.asObservable();
  forgotStatus$ = this.isForgotPassword.asObservable();
  isSa$ = this.isSa.asObservable();
  display_profile$ = this.display_profile.asObservable();

  changeRole(role) {
    this.userRole.next(role);
  }


  setProfileDisplay(displayProfile) {
    this.display_profile.next(displayProfile);
  }

    setLoginStatus(isLoggedIn) {
   this.isUserLoggedIn.next(isLoggedIn);
  }

  setSAStatus(isSa) {
    this.isSa.next(isSa);
  }

  setCSMStatus(isSa) {
    this.isCSM.next(isSa);
  }

  setForgotPassword(isUserForgotPassword) {
   this.isForgotPassword.next(isUserForgotPassword);
   console.log('forgot-idhar:' + isUserForgotPassword);
  }
}









