import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo, UserInfo } from '../shared/user-info.service';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import {Observable} from 'rxjs/Observable';
import { Router } from '@angular/router';
import { environment } from '../../environments/environment.dev';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  @ViewChild('openModal') openModal: ElementRef;
  @ViewChild('openchnModal') openchnModal: ElementRef;

  public profile_f_name: string;
  public profile_m_name: string;
  public profile_l_name: string;
  public profile_mobile: string;
  public profile_gender: string;
  public profile_email: string;
  public profile_image = 'http://www.freelanceme.net/Images/default%20profile%20picture.png';
  public profile_address= '';
  public profile_city= '';
  public profile_country= '';
  public profile_dob = '';
  public profile_ecard= '';
  public profile_other_email= '';
  public profile_business_unit= '';
  public profile_department= '';
  public profile_designation= '';
  public profile_doj= '';
  public profile_emp_no= '';
  public profile_member_id= '';
  public profile_sub_department= '';
  public profile_work_location= '';

  public errorflag = false;
  public checkflag = false;
  public errormsg = '';

  public paswd = '';
  public conf_paswd = '';

  public blood_type = '';
  public blood_polarity = '';
  public profile_blood_group = '';
  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
    private userInfo: UserInfo,
    private router: Router,
    private http: Http
  ) { }

  ngOnInit() {
    if (this.roleInfo.firsttime === true ) {
      this.openModal.nativeElement.click();
      this.roleInfo.firsttime = false;
    }
    this.profile_f_name = this.userInfo.name;
    this.profile_m_name = this.userInfo.mid_name;
    this.profile_l_name = this.userInfo.last_name;
    this.profile_gender = this.userInfo.gender;
    this.profile_email = this.userInfo.cor_email;
    this.profile_mobile = this.userInfo.phone;
    console.log('This is profile image : '+ this.userInfo.image);
    if (this.userInfo.image) {
      this.profile_image = this.userInfo.image;
    }else {
      this.profile_image = 'http://www.freelanceme.net/Images/default%20profile%20picture.png';
    }
    this.profile_address = this.userInfo.address;
    this.profile_city = this.userInfo.city;
    this.profile_country = this.userInfo.country;
    this.profile_dob = this.userInfo.dob;
    this.profile_ecard = this.userInfo.ecard;
    this.profile_other_email = this.userInfo.other_email;
    this.profile_blood_group = this.userInfo.blood_group;
    this.profile_business_unit = this.userInfo.business_unit;
    this.profile_department = this.userInfo.department;
    this.profile_designation = this.userInfo.designation;
    this.profile_doj = this.userInfo.doj;
    this.profile_emp_no = this.userInfo.employee_no;
    this.profile_member_id = this.userInfo.member_id;
    this.profile_sub_department = this.userInfo.sub_department;
    this.profile_work_location = this.userInfo.work_location;
  }


  sendProfileForm() {
    if (this.profile_other_email == null || this.profile_dob == null || this.profile_f_name == null || this.profile_l_name == null) {
      this.errormsg = 'Required field values are null';
      this.errorflag = true;
      setTimeout(()=>{    //<<<---    using ()=> syntax
            this.errorflag = false;
       }, 3000);
    } else {
    console.log("profile change submitted:");
    
    this.profile_blood_group = this.blood_type + this.blood_polarity;
    
    this.sender.sendProfileChanges (this.profile_f_name, this.profile_m_name, this.profile_l_name, this.profile_gender,this.profile_blood_group, this.profile_mobile, this.profile_email, this.profile_address, this.profile_city, this.profile_country, this.profile_dob, this.profile_ecard, this.profile_other_email, this.profile_image)
    .subscribe(
      data => {
          
          if (data.code === 200) {
              console.log("profilechanges 200 :" + data.code);
              this.checkflag = true;
              
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.checkflag = false;
               }, 3000);
               
               
               this.userInfo.name = this.profile_f_name;
               this.userInfo.mid_name = this.profile_m_name;
               this.userInfo.last_name = this.profile_l_name;
               this.userInfo.gender = this.profile_gender;
               this.userInfo.cor_email = this.profile_email;
                this.userInfo.phone = this.profile_mobile;
                this.userInfo.image = this.profile_image;
                if (this.profile_blood_group != null) {
                  this.userInfo.blood_group = this.profile_blood_group;
                }
                this.router.navigate(['/dashboard']);
                
               
          } else {
              console.log("profilechanges other :" + data.code);
              this.errorflag = true;
              this.errormsg = data.message;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.errorflag = false;
               }, 3000);
          }

      },
      error => {
        this.errorflag = true;
        
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
          console.log("profilechanges error :" + error);
      });

    }
}

imgUpload(event) {
  let fileList: FileList = event.target.files;
  let url = environment.API_URL + 'file/upload';
  let token = sessionStorage.getItem('Token');
  let n_token = token.replace(/['"]+/g, '');



  if(fileList.length > 0) {
      this.errormsg = 'Uploading file... Please Wait';
      this.errorflag = true;
      let file: File = fileList[0];
      let formData:FormData = new FormData();
      
      formData.append('file', file, file.name);
      
      let headers = new Headers();
      /** No need to include Content-Type in Angular 4 */
      //headers.append('Content-Type', 'multipart/form-data');
      headers.append('Authorization', `Bearer ${n_token}`);
      //headers.append('Accept', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.post(url, formData, options)
          .map(res => res.json())
          .catch(error => Observable.throw(error))
          .subscribe(
              data => {
                  if(data.code === 200) {
                  this.profile_image = data.link;
                  console.log('file upload success: ' + this.profile_image);
                  this.errormsg = 'File uploaded successfully';
                  this.errorflag = true;
                 
                  setTimeout(()=>{  
                          //<<<---    using ()=> syntax
                        this.errorflag = false;
                   }, 1000);
                  }
              },
              error => {
                  this.errormsg = 'Error';
                  this.errorflag = true;
                  
                  setTimeout(()=>{  
                          //<<<---    using ()=> syntax
                        this.errorflag = false;
                   }, 3000);
                  console.log(error)
              }
          )
  }
}


sendpaswd() {
  this.roleInfo.firsttime = false;
  if (this.paswd === this.conf_paswd) {
    this.sender.sendchnPswd(this.paswd)
    .subscribe(
      data => {
        console.log(data.code);
        if (data.code === 200) {
          this.errorflag = true;
          this.errormsg = data.message;
          
          setTimeout(()=>{  
            //<<<---    using ()=> syntax
          this.errorflag = false;
           }, 3000);
          this.openchnModal.nativeElement.click();
         
        } else {
          this.errorflag = true;
          this.errormsg = data.message;
          
          setTimeout(()=>{  
            //<<<---    using ()=> syntax
          this.errorflag = false;
           }, 3000);
          
        }

    },
    error => {
      this.errorflag = true;
      this.errormsg = error;
      
      setTimeout(()=>{  
        //<<<---    using ()=> syntax
      this.errorflag = false;
       }, 3000);
    });

    }else {
      this.errorflag = true;
      this.errormsg = 'Passwords donot match';
      
      setTimeout(()=>{  
        //<<<---    using ()=> syntax
      this.errorflag = false;
       }, 3000);
    }
  }

}



