import { Component, OnInit, AfterViewInit } from '@angular/core';
import { AppGlobals } from '../shared/globalflags.service';
import * as $ from 'jquery';
import { Router } from '@angular/router';
import { UserInfo, RoleInfo } from '../shared/user-info.service';
import { AuthenticationService } from '../shared/authentication.service';
import { ContainerInfo } from '../right-container/right-container.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit, AfterViewInit {

  public isLoggedIn: boolean;
  public $: any;
  public user_name: string;
  public user_image = 'http://www.freelanceme.net/Images/default%20profile%20picture.png';
  public Pusher: any;

  public user_role: string;
  public not_counter: number;
  
  public dual_roles= false;


  constructor(private roleInfo: RoleInfo,
              private _appGlobals: AppGlobals,
              private router: Router,
              private userInfo: UserInfo,
              private auth: AuthenticationService,
              private rightContainer: ContainerInfo,
              ) {
  this._appGlobals.userRole$.subscribe(value => this.user_role = value);
  this._appGlobals.isUserLoggedIn.subscribe(value => this.isLoggedIn = value);
  }

  ngOnInit() {

    /*
    console.log('navbar name: ' +  this.userInfo.name);
    this.user_name = this.userInfo.name;
    if (this.userInfo.image) {
      this.user_image = this.userInfo.image;
    }*/

    if (sessionStorage.NextToken) {
      this.dual_roles = true;
    }
  }

  ngAfterViewInit() {
        $(document).ready(function() {
      $("#panel1").click(function() {
        $("#arow1").toggleClass("fa-chevron-left");
        $("#arow1").toggleClass("fa-chevron-down");
      });
        
      $("#panel2").click(function() {
        $("#arow2").toggleClass("fa-chevron-left");
        $("#arow2").toggleClass("fa-chevron-down");
      });
        
      $("#panel3").click(function() {
        $("#arow3").toggleClass("fa-chevron-left");
        $("#arow3").toggleClass("fa-chevron-down");
      });
        
      $("#panel4").click(function() {
        $("#arow4").toggleClass("fa-chevron-left");
          $("#arow4").toggleClass("fa-chevron-down");
      });
        
      $("#panel5").click(function() {
        $("#arow5").toggleClass("fa-chevron-left");
        $("#arow5").toggleClass("fa-chevron-down");
      });
        
      $("#panel6").click(function() {
        $("#arow6").toggleClass("fa-chevron-left");
        $("#arow6").toggleClass("fa-chevron-down");
      });
        
      $("#panel7").click(function() {
        $("#arow7").toggleClass("fa-chevron-left");
        $("#arow7").toggleClass("fa-chevron-down");
      });
        
      $("#panel8").click(function() {
        $("#arow8").toggleClass("fa-chevron-left");
        $("#arow8").toggleClass("fa-chevron-down");
      });
        
      $("#panel9").click(function() {
        $("#arow9").toggleClass("fa-chevron-left");
        $("#arow9").toggleClass("fa-chevron-down");
      });
        
      $("#panel10").click(function() {
        $("#arow10").toggleClass("fa-chevron-left");
        $("#arow10").toggleClass("fa-chevron-down");
      });
        
      $("#panel11").click(function() {
        $("#arow11").toggleClass("fa-chevron-left");
        $("#arow11").toggleClass("fa-chevron-down");
      });
        
      $("#menu-icon").click(function() {
        $("#chang-menu-icon").toggleClass("fa-bars");
        $("#chang-menu-icon").toggleClass("fa-times");
        $("#show-nav").toggleClass("hide-sidebar");
        $("#show-nav").toggleClass("left-sidebar");
          
        $("#left-container").toggleClass("less-width");
        $("#right-container").toggleClass("full-width");
      });
        
     
        
    });
       //Copy in all the js code from the script.js. Typescript will complain but it works just fine
      
    }


  changeRole() {
    
    if (this.roleInfo.isHR === true) {
     
      this._appGlobals.user_change = true;
      this._appGlobals.user_type = 'emp';
      this.router.navigate(['/login']);

    }

    if (this.roleInfo.isEMP === true ) {

      this._appGlobals.user_change = true;
      this._appGlobals.user_type = 'hr';
      this.router.navigate(['/login']);
    }

    /*
    console.log("Role changed ::::::::::::::::::::" +  this.userInfo.role);
    if(this.userInfo.role === 'hr'){
      this._appGlobals.changeRole('emp');
      this._appGlobals.user_change = true;
      this._appGlobals.user_type = 'emp';
    }
    if(this.userInfo.role === 'emp') {
      this._appGlobals.changeRole('hr');
      this._appGlobals.user_change = true;
      this._appGlobals.user_type = 'hr';
    }
    
    console.log("Role changed ::::::::::::::::::::" +  this.userInfo.role);
    this.router.navigate(['login']);
    */

  }

  logoutEvent() {
    this.dual_roles = false;
    this._appGlobals.user_change = false;
    this.isLoggedIn = false;
    this._appGlobals.setLoginStatus(this.isLoggedIn);
    this.roleInfo.isEMP = false;
    this.roleInfo.isCSM = false;
    this.roleInfo.isHR = false;
    this.roleInfo.isSA = false;
    this.roleInfo.isTPA = false;
    this.roleInfo.isBU = false;
    this.roleInfo.isPA = false;

    this.roleInfo.sidestatus = false;
    sessionStorage.setItem('notCounter', '0');
    sessionStorage.removeItem('Role');
    sessionStorage.removeItem('Token');
    sessionStorage.removeItem('NextToken');
    sessionStorage.removeItem('IsLoggedIn');
    this.router.navigate(['login']);
    this.rightContainer.pre_enroll = false;
    this.rightContainer.profile = false;
    this.rightContainer.dashboard = true;
    this.rightContainer.edit = false;
    this.rightContainer.manage = false;
    this.rightContainer.create = false;
    this.rightContainer.tpa = false;
    this.auth.logout();
  }

  displayProfile() {
    this.rightContainer.profile = true;
    this.rightContainer.dashboard = false;
    this.rightContainer.manage = false;
    this.rightContainer.edit = false;
    this.rightContainer.create = false;
    this.rightContainer.tpa = false;

   
    this.rightContainer.policies = false;
    this.rightContainer.claims = false;
    this.rightContainer.service = false;
    this.rightContainer.first_time = false;
    this.rightContainer.editclaim = false;
    this.rightContainer.editcomplaint = false;
    this.rightContainer.managecorclient = false;
    this.rightContainer.addcorclient = false;
    this.rightContainer.editcorclient = false;
    this.rightContainer.pre_enroll = false;
    this.rightContainer.interpolicy = false;
    this.rightContainer.allpolicies = false;
    this.rightContainer.mypolicies = false;
  }
}

interface Message {
  text: string;
  user: string;
}



