import { Component, OnInit } from '@angular/core';
import { RoleInfo } from '../shared/user-info.service';
import { SendService } from '../shared/send.service';

@Component({
  selector: 'app-premium-utilisation',
  templateUrl: './premium-utilisation.component.html',
  styleUrls: ['./premium-utilisation.component.css']
})
export class PremiumUtilisationComponent implements OnInit {
  public policies: Policies[] = [];
  public sel_policy_index = 0;
  public send_tpa_id: number;
  public months: any;
  public premUtil: any;
  public monthId: number;
  public policy_type= '';
  public selfAmt = 0;
  public spouseAmt = 0;
  public parentsAmt = 0;
  public childAmt = 0;
  public typeOp: string;
  public types = ['inception', 'addition', 'deletion'];
  public errormsg = '';
  public errorflag = false;
  constructor(
    private roleInfo: RoleInfo,
    private sender: SendService
  ) { }

  ngOnInit() {
    if (this.roleInfo.isSA === true || this.roleInfo.isCSM === true || this.roleInfo.isBU === true) {
      this.sender.getPolicies()
      .subscribe(
        data => {
            
            console.log('no Error : ' + data.code);
            if (data.code === 200) {
                this.policies = data.data;
                console.log('here poliiiiiiii: ' + data);
                console.log('policies : ' + this.policies);
            }
            if (data.data.enrollment_start_date == null) {
              this.policy_type = 'Employer Provided Policy';
            } else {
              this.policy_type = 'Individually Procured Policy';
            }
  
        },
        error => {
            console.log('Error : ' + error);
        });
  
    }
  }

  sendPolicy(index: number) {
    this.send_tpa_id = this.policies[index].id;
    this.sender.postSendPolicyID(this.send_tpa_id)
    .subscribe (
      data => {
        if (data.code === 200) {
          this.months = data.data;
        }
      },
      error => {}
    );
    this.sender.postSendPolicyIDagain(this.send_tpa_id)
    .subscribe (
      data => {
        if (data.code === 200) {

          this.premUtil = data.data;
          this.premUtil = JSON.stringify(this.premUtil);
          this.premUtil = this.premUtil.split('{"mon', 77);
          this.premUtil.splice(0, 1);

          for(var i=0; i<this.premUtil.length; i++) {
            let buff = JSON.stringify(this.premUtil[i]);
            console.log("this is the buffer : :::::: " + buff);
            buff = buff.substr(1);
            buff = buff.substring(0, buff.indexOf('}'));
            buff = '{"mon' + buff + ' }';
            buff = buff.replace(/\\/g, '');
            this.premUtil[i] = buff;
            this.premUtil[i] = JSON.parse(this.premUtil[i]);
            console.log('This is prem : ::::::::  ' + this.premUtil[i] );
            /*
            this.premUtil[i] = this.premUtil[i].slice(0, 1);
            
            this.premUtil[i] = JSON.parse(JSON.stringify(this.premUtil[i].substring(0, this.premUtil[i].indexOf('}'))));
            this.premUtil[i] = '{"mon' + JSON.stringify(this.premUtil[i]) + '}'
            console.log('This is prem :::::::::' + JSON.stringify(this.premUtil));
            */
          }
          
        }
      },
      error => {}
    );
    }


    sendModifications() {
      let str = '{ "log_details": { "type": "' + this.typeOp + '", "month_code": ' + this.monthId + ', "policy_id": ' + this.send_tpa_id + ', "self": ' + this.selfAmt + ', "spouse": ' + this.spouseAmt + ', "parent": ' + this.parentsAmt + ', "children": ' + this.childAmt + '}}';

     this.sender.postPremiumUtil(str)
     .subscribe(
       data => {
         if (data.code === 200) {
        this.errormsg = data.message;
        this.errorflag = true;
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 2000);
        }

       },
       error => {
        this.errormsg = error;
        this.errorflag = true;
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 2000);
        
       }
     );



     this.sender.postSendPolicyIDagain(this.send_tpa_id)
     .subscribe (
       data => {
         if (data.code === 200) {
 
           this.premUtil = data.data;
           this.premUtil = JSON.stringify(this.premUtil);
           this.premUtil = this.premUtil.split('{"mon', 77);
           this.premUtil.splice(0, 1);
 
           for(var i=0; i<this.premUtil.length; i++) {
             let buff = JSON.stringify(this.premUtil[i]);
             console.log("this is the buffer : :::::: " + buff);
             buff = buff.substr(1);
             buff = buff.substring(0, buff.indexOf('}'));
             buff = '{"mon' + buff + ' }';
             buff = buff.replace(/\\/g, '');
             this.premUtil[i] = buff;
             this.premUtil[i] = JSON.parse(this.premUtil[i]);
             console.log('This is prem : ::::::::  ' + this.premUtil[i] );
             /*
             this.premUtil[i] = this.premUtil[i].slice(0, 1);
             
             this.premUtil[i] = JSON.parse(JSON.stringify(this.premUtil[i].substring(0, this.premUtil[i].indexOf('}'))));
             this.premUtil[i] = '{"mon' + JSON.stringify(this.premUtil[i]) + '}'
             console.log('This is prem :::::::::' + JSON.stringify(this.premUtil));
             */
           }
           
         }
       },
       error => {}
     );

     }

}


export interface Policies {
  id: number;
  name: string;
  active: number;
  created_at: string;
  updated_at: string;
  policy_number: string;
  insurer_id: number;
  sum_assured: number;
  benefits: string;
  eligibility: string;
  faqs: string;
  others: string;
  coverage_type: string;
  premium_utilization: string;
  cd_balance: string;
  current_policy_period: string;
  organization_name: string;
  image: string;
  policy_type: string;
}


