import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-add-content',
  templateUrl: './add-content.component.html',
  styleUrls: ['./add-content.component.css']
})
export class AddContentComponent implements OnInit {
  public errormsg = '';
  public errorflag = false;
  public con_type = '';
  public con_name = '';
  public con_desc = '';
  public con_link = '';
  constructor(
    private roleInfo: RoleInfo,
    private sender: SendService
  ) { }

  ngOnInit() {
  }

  AddConPost () {
    console.log('Form details: ' + this. con_type + this.con_name + this.con_desc + this.con_link);
    this.sender.postContentAdd (this.con_type, this.con_name, this.con_desc, this.con_link, '2017-11-14 00:00:00')
    .subscribe (
      data => {
        this.errormsg = data.message;
        this.errorflag = true;
        setTimeout(() => {    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
      },
      error => {
        this.errormsg = error;
        this.errorflag = true;
        setTimeout(() => {    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
      }
    );
  }

}



