import { Component, OnInit } from '@angular/core';
import { RoleInfo } from '../shared/user-info.service';
import { SendService } from '../shared/send.service';

@Component({
  selector: 'app-adddependents',
  templateUrl: './adddependents.component.html',
  styleUrls: ['./adddependents.component.css']
})
export class AdddependentsComponent implements OnInit {
public errormsg = '';
public errorflag = false;

public add_d_fname='';
public add_d_mname='';
public add_d_lname='';
public add_d_gender='';
public add_d_dob='';
public add_d_relation='';

public policies: Policies[] = [];
public sel_pol_id: number;
public relatives: any;

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
  ) { }

  ngOnInit() {
    this.sender.getSubdPolicies('emp')
    .subscribe(
      data => {
          
          console.log('no Error : ' + data.code);
          if (data.code === 200) {
              this.policies = data.data;
              console.log('here poliiiiiiii: ' + data);
              console.log('policies : ' + this.policies);
          }
          

      },
      error => {
          console.log('Error : ' + error);
      });

      this.sender.postGetRelations(this.sel_pol_id)
      .subscribe(
        data => {
          
          console.log('no Error : ' + data.code);
          if (data.code === 200) {
              
          }
          

      },
      error => {
          console.log('Error : ' + error);
      }
      );
  }

// EMP utility functions start
addNewDependent() {
  
  console.log("Add dependents data : " + this.add_d_fname + this.add_d_mname + this.add_d_lname + this.add_d_dob + this.add_d_gender + this.add_d_relation);
  this.sender.sendNewDependent(this.add_d_fname, this.add_d_mname, this.add_d_lname, this.add_d_dob, this.add_d_relation, this.add_d_gender)
  .subscribe(
      data => {
         console.log(data.code);
         console.log(data.message);
          if (data.code === 200) {
              this.errormsg = data.message;
              this.errorflag = true;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.errorflag = false;
               }, 3000);
          }else {
             this.errormsg = data.message;
             this.errorflag = true;
             setTimeout(()=>{    //<<<---    using ()=> syntax
                   this.errorflag = false;
              }, 3000);
          }
 
      },
      error => {
        this.errormsg = error;
        this.errorflag = true;
        setTimeout(()=>{    //<<<---    using ()=> syntax
              this.errorflag = false;
         }, 3000);
      });
}
// EMP utility function ends

getRelations (pol_id) {
  this.sender.postGetRelations(pol_id)
  .subscribe (
    data => {
      this.relatives = data.data;
    },
    error => {}
  );
}

}


export interface Policies {
  id: number;
  name: string;
  active: number;
  created_at: string;
  updated_at: string;
  policy_number: string;
  insurer_id: number;
  sum_assured: number;
  benefits: string;
  eligibility: string;
  faqs: string;
  others: string;
  coverage_type: string;
  premium_utilization: string;
  cd_balance: string;
  current_policy_period: string;
  organization_name: string;
  image: string;
  policy_type: string;
}



