import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

declare var jQuery: any;

@Component({
  selector: 'app-managecsm',
  templateUrl: './managecsm.component.html',
  styleUrls: ['./managecsm.component.css']
})
export class ManagecsmComponent implements OnInit {


public orgToCSM: OrgtoCSM[] = [];
public allCSM: AllCSM[] = [];
public allOrg: AllOrg[] = [];
public selectedCSM: number;
public selectedOrg: number;
public errorflag = false;
public errormsg = '';

public page_serial = 0;
public hideremoveModal = false;

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo,
    private router: Router
  ) { }

  ngOnInit() {
  // BU init starts
    if (this.roleInfo.isBU === true || this.roleInfo.isSA === true) {
      this.sender.getOrg()
      .subscribe(
        data => {
          if (data.code === 200) {
          this.allOrg = data.data;
          console.log('This are the orgs :'+ this.allOrg);
          }
        },
        error => {}
      )

      this.sender.getOrgCSM()
      .subscribe(
        data => {
          if (data.code === 200) {
            console.log(data.message);
            this.orgToCSM = data.data;
          }
        },
        error => {}
      );

      this.sender.getAllCSM()
      .subscribe(
        data => {
          if (data.code === 200) {
            this.allCSM = data.data;
          }
        },
        error => {}
      );

    }
  // BU init ends
  }
// BU utility starts
CSMAddSubmit(org_id, CSM_id) {
  this.sender.addCSM(org_id, CSM_id)
  .subscribe(
    data => {
      if (data.code === 200) {
        this.ngOnInit();
        this.errorflag = true;
        this.errormsg = data.message;
        setTimeout(() => {    //<<<---    using ()=> syntax
          this.errorflag = false;
          this.errormsg = '';
     }, 3000);
      }
      this.errorflag = true;
      this.errormsg = data.message;
      setTimeout(() => {    //<<<---    using ()=> syntax
        this.errorflag = false;
        this.errormsg = '';
   }, 3000);
    },
    error => {
      this.errorflag = true;
      this.errormsg = error;
      setTimeout(()=>{    //<<<---    using ()=> syntax
        this.errorflag = false;
        this.errormsg = '';
   }, 3000);
    }
  );
}

CSMRemoveSubmit(org_id, CSM_id, i) {
  this.sender.removeCSM(org_id, CSM_id)
  .subscribe(
    data => {
      if (data.code === 200) {
        this.ngOnInit()
        this.errorflag = true;
        this.errormsg = data.message;
        setTimeout(() => {    //<<<---    using ()=> syntax
          this.errorflag = false;
          this.errormsg = '';
     }, 3000);
      }
      
      this.errorflag = true;
      this.errormsg = data.message;
      setTimeout(() => {    //<<<---    using ()=> syntax
        this.errorflag = false;
        this.errormsg = '';
   }, 3000);
    },
    error => {
      this.errorflag = true;
      this.errormsg = error;
      setTimeout(()=>{    //<<<---    using ()=> syntax
        this.errorflag = false;
        this.errormsg = '';
   }, 3000);
    }
  );
}
// BU utility ends
}

export interface OrgtoCSM {
firstname: string;
lastname: string;
organization_id: number;
organization_name: string;
organization_status: number;
user_id: number;
}

export interface AllCSM {
firstname: string;
id: number;
lastname: string;
organization:
{
  active: number;
  address: string;
  city: string;
  country: string;
  created_at: string;
  description: string;
  email: string;
  id: number;
  image: string;
  is_broker: number;
  is_insurer: number;
  is_tpa: number;
  name: string;
  state: string;
  tpa_id: number;
  updated_at: number;
};

organization_id: number;
}

export interface AllOrg {
  active: number;
  address: string;
  city: string;
  country: string;
  created_at: string;
  description: string;
  email: string;
  id: string;
  image: string;
  is_broker: string;
  is_insure: string;
  is_tpa: string;
  name: string;
  state: string;
  tpa_id: number;
  updated_at: string;
}


