import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';
@Component({
  selector: 'app-manageservicerequest',
  templateUrl: './manageservicerequest.component.html',
  styleUrls: ['./manageservicerequest.component.css']
})
export class ManageservicerequestComponent implements OnInit {
  public errorflag = false;
  public errormsg = '';

  public complaints: Complaint[] = [];
  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo
  ) { }

  ngOnInit() {
    // CSM init starts
    if (this.roleInfo.isCSM === true || this.roleInfo.isBU === true || this.roleInfo.isSA === true) {
    this.sender.getComplaints()
    .subscribe(
      data => {
         console.log(data.code);
         console.log(data.message);
          if (data.code === 200) {
              console.log("from complaints: " + data.data);
              this.complaints = data.data;
             
              
          }else {
             this.errormsg = data.message;
             this.errorflag = true;
             setTimeout(()=>{    //<<<---    using ()=> syntax
                   this.errorflag = false;
              }, 3000);
          }
 
      },
      error => {
          console.log('Error : ' + error);
      });
    }
    // CSM init ends 
  }

}

export interface Complaint {
  title: string;
  description: string;
  category: string;
}


