import { Component, OnInit } from '@angular/core';
import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

import { ElementRef, NgZone, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { } from 'googlemaps';
import { MapsAPILoader } from '@agm/core';
import { AgmMap } from '@agm/core';

declare var google: any;




@Component({
  selector: 'app-nearby-hospitals',
  templateUrl: './nearby-hospitals.component.html',
  styleUrls: ['./nearby-hospitals.component.css']
})
export class NearbyHospitalsComponent implements OnInit {

  labelOptions = {
    color: '#2e3192',
    fontFamily: '',
    fontSize: '14px',
    fontWeight: 'bold',
    text: 'Your location',
    };

  @ViewChild('map') mapElement: ElementRef;
  map: any;

  places : Array<any>;


  
  title: string = 'My first AGM project';
  lat: number = 51.678418;
  lng: number = 7.809007;
  public geolocationPosition: any;

  @ViewChild(AgmMap)
  public agmMap: AgmMap;


  
  
   public latitude: number;
   public longitude: number;
   public searchControl: FormControl;
   public zoom: number;
  
   @ViewChild('search')
   public searchElementRef: ElementRef;

  constructor(
    private mapsAPILoader: MapsAPILoader,
    private ngZone: NgZone,
    private roleInfo: RoleInfo,
    private sender: SendService
  ) { }

  ngOnInit () {
    if (window.navigator && window.navigator.geolocation) {
      window.navigator.geolocation.getCurrentPosition(
          position => {
              this.geolocationPosition = position;
              this.lat = position.coords.latitude;
              this.lng = position.coords.longitude;
                  console.log(position);

                  let latLng = new google.maps.LatLng(this.lat, this.lng);
                  
                      let mapOptions = {
                      center: latLng,
                      zoom: 15,
                      mapTypeId: google.maps.MapTypeId.ROADMAP
                      };
                  
                      this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
                  
                      this.getRestaurants(latLng).then((results : Array<any>)=>{
                          this.places = results;
                          console.log ('places', this.places);
                        
                      },(status)=>console.log(status));
          },
          error => {
              switch (error.code) {
                  case 1:
                      console.log('Permission Denied');
                      break;
                  case 2:
                      console.log('Position Unavailable');
                      break;
                  case 3:
                      console.log('Timeout');
                      break;
              }
          }
      );
  }

  //set google maps defaults
  this.zoom = 4;
  this.latitude = this.lat;
  this.longitude = this.lng;

  //create search FormControl
  this.searchControl = new FormControl();

  //set current position
  this.setCurrentPosition();

  //load Places Autocomplete
  this.mapsAPILoader.load().then(() => {
    let autocomplete = new google.maps.places.Autocomplete(this.searchElementRef.nativeElement, {
      types: ["address"]
    });
    autocomplete.addListener("place_changed", () => {
      this.ngZone.run(() => {
        //get the place result
        let place: google.maps.places.PlaceResult = autocomplete.getPlace();

        //verify result
        if (place.geometry === undefined || place.geometry === null) {
          return;
        }

        //set latitude, longitude and zoom
        this.latitude = place.geometry.location.lat();
        this.longitude = place.geometry.location.lng();
        this.lat = this.latitude;
        this.lng = this.longitude;
        this.zoom = 12;

        let latLng = new google.maps.LatLng(this.lat, this.lng);
        
            let mapOptions = {
            center: latLng,
            zoom: 15,
            mapTypeId: google.maps.MapTypeId.ROADMAP
            };
        
            // this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
        
            this.getRestaurants(latLng).then((results : Array<any>)=>{
                this.places = results;
                console.log ('places', this.places);
              
            },(status)=>console.log(status));
      });
    });
  });
  

  
    
        

    
    
    


  }


  private setCurrentPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((position) => {
        this.latitude = position.coords.latitude;
        this.longitude = position.coords.longitude;
        this.zoom = 12;
      });
    }
  }
  





  addMap(lat,long){
    
        let latLng = new google.maps.LatLng(lat, long);
    
        let mapOptions = {
        center: latLng,
        zoom: 15,
        mapTypeId: google.maps.MapTypeId.ROADMAP
        }
    
        this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
    
        this.getRestaurants(latLng).then((results : Array<any>)=>{
            this.places = results;
            for(let i = 0 ;i < results.length ; i++)
            {
                this.createMarker(results[i]);
            }
        },(status)=>console.log(status));
    
        this.addMarker();
    
    }
    

    addMarker(){
      
          let marker = new google.maps.Marker({
          map: this.map,
          animation: google.maps.Animation.DROP,
          position: this.map.getCenter()
          });
      
          let content = "<p>This is your current position !</p>";          
          let infoWindow = new google.maps.InfoWindow({
          content: content
          });
      
          google.maps.event.addListener(marker, 'click', () => {
          infoWindow.open(this.map, marker);
          });
      
      }

      getUserPosition(){
      
       
            this.addMap(this.lat, this.lng);
    
      
    }

    getRestaurants(latLng)
    {
        var service = new google.maps.places.PlacesService(this.map);
        let request = {
            location : latLng,
            radius : 3047 ,
            types: ["hospital"]
        };
        return new Promise((resolve,reject)=>{
            service.nearbySearch(request,function(results,status){
                if(status === google.maps.places.PlacesServiceStatus.OK)
                {
                    resolve(results);    
                }else
                {
                    reject(status);
                }
    
            }); 
        });
    
    }

    createMarker(place)
    {
        let marker = new google.maps.Marker({
        map: this.map,
        animation: google.maps.Animation.DROP,
        position: place.geometry.location
        });   
    } 

   

}



