import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-addtpa',
  templateUrl: './add-tpa-api.component.html',
  styleUrls: ['./add-tpa-api.component.css']
})
export class AddTpaApiComponent implements OnInit {
  public tpa_name: string;
  public tpa_type: string;
  public tpa_des: string;
  public tpa_address: string;
  public tpa_city: string;
  public tpa_state: string;
  public tpa_country: string;
  public tpa_mail: string;
  public tpa_number: string;
  public tpa_username: string;
  public tpa_password: string;
  public tpa_url: string;
  public tpa_methodname: string;
  public self_key: string[] = [];
  public port_key: string[] = [];
  public tpa_request_username: string;
  public tpa_request_password: string;
  public tpa_request_policy: string;
  public tpa_request_startdate: string;
  public tpa_request_enddate: string;
  public man_self_key: string[] = [];
  public man_port_key: string[] = [];

  public checkflag = false;
  public errorflag = false;
  public errormsg = '';
  public divCount = [];
  public n_divCount = [];
  



  public formnum = 1;

  constructor(
    private sender: SendService,
    private roleInfo: RoleInfo
  ) { }

  ngOnInit() {
    if (this.roleInfo.changePage === 'Keys are not mapped.') {
      this.errormsg = 'Keys not mapped.\n Please provide necessary information.';
      this.errorflag = true;
      setTimeout(()=>{    //<<<---    using ()=> syntax
            this.errorflag = false;
       }, 3000);
       this.formnum = 2;
    }
  }

  addTPA () {
    let sendstr: string;
    let editstr: string;
    let buff_str_one: string;
    let buff_str_two: string;
    console.log('tap :' + this.tpa_name + this.tpa_type + this.tpa_des);

    console.log('fields: ' + this.self_key + this.port_key);
    editstr = '{ "tpa_details" : { "tpa_id": ' + this.roleInfo.tpa_id + ', ';
    editstr += '"username": "' + this.tpa_username + '", "password": "' + this.tpa_password + '", ';

    sendstr = '{"meta": {"name": "' + this.tpa_name + '", "type": "tpa", "description": "' + this.tpa_des + '",  "image" : "http://link", "city": "' + this.tpa_city + '", "address": "' + this.tpa_address + '", "state": "' + this.tpa_state + '", "country": "' + this.tpa_country + '", ';
    sendstr += '"email": "' + this.tpa_mail + '", "tpa_number": "' + this.tpa_number + '", "username": "' + this.tpa_username + '", "password": "' + this.tpa_password + '" ';
    
    if (this.port_key.length > 0){
    sendstr += ', "api": ';
    editstr += ' "api": ';
    buff_str_one = '{"url":"' + this.tpa_url + '","claims":{"method_name":"' + this.tpa_methodname + '","request":{"username_field":"' + this.tpa_request_username + '","password_field":"' + this.tpa_request_password + '","policy_number_field":"' + this.tpa_request_policy + '","start_date_field":"' + this.tpa_request_startdate + '","end_date_field":"' + this.tpa_request_enddate + '"},"response":{';
    for (var i=0; i<this.port_key.length; i++) {
      buff_str_one += '"' + this.port_key[i] + '":"' + this.self_key[i] + '",';
    }
    buff_str_one = buff_str_one.slice(0, -1);
    buff_str_one += '}}}';

    sendstr += JSON.stringify(buff_str_one);
    editstr += JSON.stringify(buff_str_one);
    editstr += ' }}';
  }
    
    if(this.man_port_key.length > 0){
      sendstr += ', "tpa_claim_map": ';
      buff_str_two = '{';
    for (var j=0; j<this.man_port_key.length; j++) {
      buff_str_two += '"' + this.man_port_key[j] + '":"' + this.man_self_key[j] + '",';
    }
    buff_str_two = buff_str_two.slice(0, -1);
    buff_str_two += '}';
    sendstr += JSON.stringify(buff_str_two);
    }
    sendstr += '}}';
    
    if (this.roleInfo.changePage === 'Keys are not mapped.') {
      this.sender.postAPIMatchedKeys (editstr)
      .subscribe (
        data => {
          this.roleInfo.changePage = '';
          this.errorflag = true;
          this.errormsg = data.message;
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
        },
        error => {
          this.roleInfo.changePage = '';
        }
      );
    } else {
      this.sender.sendTPAForm (sendstr)
      .subscribe(
        data => {
            
            if (data.code === 200) {
                console.log("tpa 200 :" + data.code);
                this.checkflag = true;
                
                setTimeout(()=>{    //<<<---    using ()=> syntax
                      this.checkflag = false;
                 }, 3000);
                 this.tpa_name = '';
                 this.tpa_type = '';
                 this.tpa_des = '';
                 this.errorflag = true;
                 this.errormsg = data.message;
                 setTimeout(()=>{    //<<<---    using ()=> syntax
                       this.errorflag = false;
                  }, 3000);
            } else {
                console.log("tpa other :" + data.code);
                this.errorflag = true;
                
                setTimeout(()=>{    //<<<---    using ()=> syntax
                      this.errorflag = false;
                 }, 3000);
                 this.tpa_name = '';
                 this.tpa_type = '';
                 this.tpa_des = '';
            }
  
        },
        error => {
          this.errorflag = true;
          
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           }, 3000);
            console.log("tpa error :" + error);
            this.tpa_name = '';
            this.tpa_type = '';
            this.tpa_des = '';
        });
    }
    
}

addUserField () {
  this.divCount.push(1);
  console.log(this.divCount);
  
}

removeUserField () {
  console.log("popopopopopopopop");
  this.divCount.pop();
}

n_addUserField () {
  this.n_divCount.push(1);
  console.log(this.divCount);
  
}

n_removeUserField () {
  console.log("popopopopopopopop");
  this.n_divCount.pop();
}


nextForm () {
  this.formnum += 1;
}

prevForm () {
  this.formnum -= 1;
}

manualForm() {
  this.formnum = 4;
}

prevmanForm() {
  this.formnum = 1;
}
}



