import { Component, OnInit } from '@angular/core';
import { RoleInfo } from '../shared/user-info.service';
import { AppGlobals } from '../shared/globalflags.service';

import { fadeInAnimation } from '../_animations/index';

declare var Pusher: any;
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
  // make fade in animation available to this component
  animations: [fadeInAnimation],
  // attach the fade in animation to the host (root) element of this component
  host: { '[@fadeInAnimation]': '' }
})
export class DashboardComponent implements OnInit {

  constructor(
    private roleInfo: RoleInfo,
    private _appGlobals: AppGlobals,
  ) { }

  ngOnInit() {
    let flag = 0;
    
    
    
    // on page reload
    if ( this._appGlobals.user_change  !== true) {
    if (sessionStorage.getItem('Token')) {
      
      if (sessionStorage.getItem('Role') === '"emp"') {
       this.roleInfo.isEMP = true;
      }
      if (sessionStorage.getItem('Role') === '"hr"') {
        this.roleInfo.isHR = true;
      }
      if (sessionStorage.getItem('Role') === '"csm"') {
        this.roleInfo.isCSM = true;
      }
      if (sessionStorage.getItem('Role') === '"tpa"') {
        this.roleInfo.isTPA = true;
      }
      if (sessionStorage.getItem('Role') === '"sa"') {
        this.roleInfo.isSA = true;
      }
      if (sessionStorage.getItem('Role') === '"bu"') {
        this.roleInfo.isBU = true;
      }
    }
  }


  if (sessionStorage.Flag) {
    
  
    sessionStorage.removeItem('Flag');
  // notification 
  var x = document.getElementById('notification');
  var not_counter = 0;
  x.style.display = 'none';

  //Notification.requestPermission();

  // Enable pusher logging - don't include this in production
  
  Pusher.logToConsole = true;
  let token = sessionStorage.getItem('Token');
  sessionStorage.setItem("notCounter", '0');
  sessionStorage.setItem("flag", '1')
  let n_token = token.replace(/['"]+/g, '');
  let str1 = 'Bearer ';
  let str = str1.concat(n_token);
  
  
var pusher = new Pusher('9e451a0df8e82e76773e', {
  cluster: 'ap2',
  encrypted: true,
  auth: {
     headers: {
      Authorization: str
    }
  },
  authEndpoint: "http://52.11.67.218/api/v1/pusher/auth"
});

//Subscribing to channels

var channel = pusher.subscribe('private-alerts');
channel.bind('alerts', function(data) {
  not_counter = parseInt(sessionStorage.getItem("notCounter"));  
  console
  not_counter += 1;
  sessionStorage.setItem("notCounter", <string><any>not_counter);
  x.style.display = 'block'; 
  var buff_string = '<li><div class="col-md-12 col-sm-12 col-xs-12 pd-l0 itemo" ><a>' + data.message + '</a> <a  class="rIcon"><i class="fa fa-dot-circle-o"></i></a><br><p class="time">' + new Date().toLocaleString(); + '</p></div></li>';
  var count_div = document.getElementById('notif_count');
  var pusher_div = document.getElementById('pusher');

  
  
  console.log('this is buff_string' + buff_string);
  pusher_div.innerHTML = buff_string + pusher_div.innerHTML;
  console.log('this is pusher_div' + pusher_div);
  count_div.innerHTML = <string><any>not_counter ;
  x.innerHTML = '<div class="row"><button type="button" class="btn btn-primary" onclick="show()">' + data.message + '</button></div>'
  console.log(data.message);
  setTimeout(()=>{    //<<<---    using ()=> syntax
      x.style.display = 'none'; 
  }, 3000);

});

  // notification ends
  }


  flag = 1;
  }

}



