import { Component, OnInit } from '@angular/core';

import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';

@Component({
  selector: 'app-addcompany',
  templateUrl: './addcompany.component.html',
  styleUrls: ['./addcompany.component.css']
})
export class AddcompanyComponent implements OnInit {

  public errorflag = false;
  public errormsg = '';

  public cor_tpa: TPA[] = [];


  public addCor_tpa: number;
  public addCor_name= '';
  public addCor_address = '';
  public addCor_city = '';
  public addCor_state = '';
  public addCor_country = '';
  public addCor_description = '';
  public addCor_email = '';
  public addCor_type: number;

  constructor(
    private roleInfo: RoleInfo,
    private sender: SendService
  ) { }

  ngOnInit() {
    // CSM init starts
    if (this.roleInfo.isCSM === true || this.roleInfo.isBU === true || this.roleInfo.isSA === true) {
    this.sender.getTpaCor () 
    .subscribe(
        data => {
            
            console.log('no Error : ' + data.code);
            if (data.code === 200) {
              this.cor_tpa = data.data;
            }else {
                this.errormsg = data.message;
                this.errorflag = true;
                setTimeout(()=>{    //<<<---    using ()=> syntax
                      this.errorflag = false;
                 }, 3000);
            }

        },
        error => {
            console.log('Error : ' + error);
        });
      }
    // CSM init ends
  }
// CSM utility starts
AddCorPost (name: string, tpa_id: number, address: string, city: string, state: string, country: string, description: string, email: string, type: number) {
  console.log('type1 ++++++++++++++++'+ type);
  this.sender.addCor(name, tpa_id, address, city, state, country, description, email, type)
  .subscribe(
      data => {
          
          console.log('no Error : ' + data.code);
          if (data.code === 200) {
              this.addCor_name = '';
              this.addCor_tpa = null;
              this.errormsg = data.message;
              this.errorflag = true;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.errorflag = false;
               }, 3000);
              
          }

      },
      error => {
          console.log('Error : ' + error);
      });
}

// CSM utility ends
}


export interface TPA {
  id: number;
  name: string;
  active: number;
  created_at: string;
  updated_at: string;
  description: string;
  image: string;
  tat: number;
}



