import { Component, OnInit } from '@angular/core';
import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { Validators, FormGroup, FormArray, FormBuilder } from '@angular/forms';
import {Observable} from 'rxjs/Observable';


import { SendService } from '../shared/send.service';
import { RoleInfo } from '../shared/user-info.service';
import { environment } from '../../environments/environment.dev';

@Component({
  selector: 'app-addemployee',
  templateUrl: './addemployee.component.html',
  styleUrls: ['./addemployee.component.css']
})
export class AddemployeeComponent implements OnInit {
    public email: string[] = [];
    public role_id: number[] = [];
    public org_id: number[] = [];
  public org: Org[];
  public roles: Roles[];
  public org1: Org[];
  public roles1: Roles[];
  public divCount = [];
  
  public e_count = 0;
  public r_count = 0;
  public o_count = 0;
  
  public policy_state = true;
 
  
  public errormsg = '';
  public errorflag = false;
  public checkflag = false;
  
  public pre_enroll= false;

  public all_roles: All_roles[] = [];
  public all_org: All_Org[] = [];
  


  public policies: Policies[] = [];

  constructor(
      private sender: SendService,
      private roleInfo: RoleInfo,
      private http: Http,

    ) {
      this.roles = [];
      this.org = [];
      this.org1 = [];
      this.roles1 = [];
     }
  
    ngOnInit() {
      // HR init functions start
      if (this.roleInfo.isHR === true) {
        this.sender.getOrg()
        .subscribe(
          data => {
              
              console.log('no Error : ' + data.code);
              if (data.code === 200) {
                  this.org = data.data;
                  this.org1 = data.data;
                  
                  console.log('org : ' + this.org);
              }
    
          },
          error => {
              console.log('Error : ' + error);
          });
    
          this.sender.getRole()
          .subscribe(
            data => {
               
                console.log('no Error role clicker : ' + data.code);
                if (data.code === 200) {
                      this.roles = data.data;
                      this.roles1 = data.data;
                      
                    console.log('role : ' + this.roles);
                      
            
                }
    
            },
            error => {
                console.log('Error : ' + error);
            });

            this.sender.getPolicies()
            .subscribe(
              data => {
                  
                  console.log('no Error : ' + data.code);
                  if (data.code === 200) {
                      this.policies = data.data;
                     
                  }
                  
        
              },
              error => {
                  console.log('Error : ' + error);
              });
    
      }// HR init functions end
    }
  
  // HR utility functions start
   
  stateChange () {
    this.policy_state = !this.policy_state;
  }

  addUserField () {
    this.divCount.push(1);
    console.log(this.divCount);
    this.e_count += 1;
    this.r_count += 1;
    this.o_count += 1;
  }
  
  removeUserField () {
    console.log("popopopopopopopop");
    this.divCount.pop();
  }
  
  
  fileChange(event) {
    let fileList: FileList = event.target.files;
    let url = environment.API_URL + 'account/create';
    let token = localStorage.getItem('UserToken');
    let n_token = token.replace(/['"]+/g, '');
  
  
    console.log('pre-enroll: ' + this.pre_enroll);
    if(fileList.length > 0) {
        let file: File = fileList[0];
        let formData:FormData = new FormData();
        formData.append('single', 'false');
        formData.append('file', file, file.name);
        formData.append('pre_enrollment', <string><any>this.pre_enroll);
        let headers = new Headers();
        /** No need to include Content-Type in Angular 4 */
        //headers.append('Content-Type', 'multipart/form-data');
        headers.append('Authorization', `Bearer ${n_token}`);
        //headers.append('Accept', 'application/json');
        let options = new RequestOptions({ headers: headers });
        this.http.post(url, formData, options)
            .map(res => res.json())
            .catch(error => Observable.throw(error))
            .subscribe(
                data => {
                    if(data.code === 200) {
                        this.pre_enroll = false;
                    console.log('file upload success: ' + data.code);
                    this.errormsg = 'File uploaded successfully';
                    this.errorflag = true;
                    
                    setTimeout(()=>{  
                            //<<<---    using ()=> syntax
                          this.errorflag = false;
                     }, 3000);
                    }
                },
                error => {
                    this.errormsg = 'Error';
                    this.errorflag = true;
                    
                    setTimeout(()=>{  
                            //<<<---    using ()=> syntax
                          this.errorflag = false;
                     }, 3000);
                    console.log(error)
                }
            )
    }
  }
  
  onSubmit () {
    console.log('result:' + this.email + ' ' + this.role_id + ' ' + this.org_id);
    this.sender.sendNewUsers (this.email, this.role_id, this.org_id)
    .subscribe(
      data => {
          console.log("senduser :" + data.code);
          if (data.code === 200) {
              this.email = [];
              this.role_id = [];
              this.org_id = [];
              
              this.checkflag = true;
              this.errormsg = data.message;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.checkflag = false;
               }, 3000);
          } else {
              this.email = [];
              this.role_id = [];
              this.org_id = [];
              
              this.errorflag = true;
              this.errormsg = data.message;
              setTimeout(()=>{    //<<<---    using ()=> syntax
                    this.errorflag = false;
               }, 3000);
          }
  
      },
      error => {
          this.email = [];
          this.role_id = [];
          this.org_id = [];
          this.errorflag = true;
          this.errorflag = true;
          
          setTimeout(()=>{    //<<<---    using ()=> syntax
                this.errorflag = false;
           },3000);
          console.log("senduser-error: " + error);
      });
  }
  
  // HR utility functions end
}


export interface Org
{  
    id: number;  
    name: string;  
    tpa_id: number;
    active: boolean;
    created_at: string;
    updated_at: string;
}

export interface Roles  
{  
    id: number;  
    role: string;
    created_at: string;
    updated_at: string;
}

export interface All_Org {
    name: string;
    id: number;
}

export interface All_roles {
    role: string;
    id: number;
}

export interface Policies {
    id: number;
    name: string;
    active: number;
    created_at: string;
    updated_at: string;
    policy_number: string;
    insurer_id: number;
    sum_assured: number;
    benefits: string;
    eligibility: string;
    faqs: string;
    others: string;
    coverage_type: string;
    premium_utilization: string;
    cd_balance: string;
    current_policy_period: string;
    organization_name: string;
    image: string;
    policy_type: string;
}


